import { ChangePageDataObject }
from '../../../../../shared/system-shared/model/data-objects/event-system/functions/change-page.data-object';
import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { InjectorService } from '../../../../../shared/system-shared/model/services/injector.service';
import { PageService } from '../../../../../shared/system-shared/model/services/page.service';
import { UnitService } from '../../../../../shared/system-shared/model/services/unit.service';
import { FunctionPlayableDataObjectInterface } from './function.playable.data-object.interface';
import { FunctionPlayableDataObjectCollection } from '../function.playable.data-object.collection';



/**
 * Playable version of ChangePage data object
 */
export class ChangePagePlayableDataObject extends ChangePageDataObject
    implements FunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

	/**
     * TODO: Move this constant to ChangePageLibraryDataObject when available
	 * <p>Type value to load the next page in the course sequence.</p>
	 */
    public static readonly TYPE_NEXT_PAGE: string = 'NextPage';

	/**
     * TODO: Move this constant to ChangePageLibraryDataObject when available
	 * <p>Type value to load the next page in the course sequence.</p>
	 */
    public static readonly TYPE_PREVIOUS_PAGE: string = 'PreviousPage';

	/**
     * TODO: Move this constant to ChangePageLibraryDataObject when available
	 * <p>Type value to load the next page in the course sequence.</p>
	 */
    public static readonly TYPE_SELECT_PAGE: string = 'SelectPage';


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableChangePageLogger: Logger;

    private pageService: PageService = InjectorService.injector.get(PageService);

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableChangePageLogger = Log.getInstance().getLogger('ChangePagePlayableDataObject');
        this.playableChangePageLogger.debug('constructorPreprocessing(): Playable ChangePage');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


    /**
     * <p>Run ChangePage.</p>
     *
     */
    public runFunction(): void {
        this.playableChangePageLogger.debug('runFunction(): type = ' + this.type + ' pageID = ' + this.pageID );

        switch (this.type) {
            // TODO: Get the following constant from ChangePageLibraryDataObject when available
            // for example, case ChangePageLibraryDataObject.TYPE_NEXT_PAGE:
            case ChangePagePlayableDataObject.TYPE_NEXT_PAGE:
                this.pageService.loadNextPage();
                break;
            case ChangePagePlayableDataObject.TYPE_PREVIOUS_PAGE:
                this.pageService.loadPreviousPage();
                break;
            case ChangePagePlayableDataObject.TYPE_SELECT_PAGE:
                this.pageService.loadPage(this.pageID);
                break;
        }

        /* NOTE:	Unlike other functions, loading another page should prevent any subsequent functions from running.
            To ensure consistency, childProcessComplete not called.  (Otherwise subsequent functions that run
            almost immediately could sometimes execute properly while the player makes the asynchronous requests
            needed to load another page)
        */
        // (this.parent as FunctionPlayableDataObjectCollection).childProcessComplete();

    }

}



