import { Component, Input, Output, EventEmitter } from '@angular/core';
import { PageService } from '../../shared/system-shared/model/services/page.service';
import { UnitService } from '../../shared/system-shared/model/services/unit.service';

@Component({
    selector: "player-app-bar",
    templateUrl: 'app-bar.component.html',
    styleUrls:  [ 'user-interface.module.css' ]
})

/**
 * TODO: Comment Me!!
 */
export class AppBarComponent {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The height of the app bar
     * TODO: Maybe this should just be height???
     *
     */
    @Input()
    appBarHeight: number;

    @Output() menuButtonClicked: EventEmitter<any> = new EventEmitter<any>();


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor for the Control Bar Component. Used to inject pageService and unitService
     *
     * @param pageService
     * @param unitService
     */
    constructor(private pageService: PageService, private unitService: UnitService) {}


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////





    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Event Handlers
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Menu Button Click handler. Emit an Event to let the app component know we have been clicked.
     */
    public onMenuButtonClicked (): void {
        this.menuButtonClicked.emit();
    }


}



