import { LogLevel } from 'typescript-logging';
import { Log, Logger } from '../../log';
import { XmlParser } from '../transactions/xml-parser';

/**
 * data object represents data from online/offline assets/config.xml file
 *
 */
export class ConfigDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


    public enableBreadcrumb: boolean;
    public lockProgress: boolean;
    public filter: string; // Not implemented yet
    public logLevel: LogLevel;
    public maxCourseMapLevel: string;
    public pageNumberLevel: string;
    public playerWidth: number;
    public playerHeight: number;
    public playerCode: string;

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new ConfigDataObject instance.
     *
     * @param   sourceXml
     */
    public constructor(sourceXml?: Element) {
        if (sourceXml) {
            this.constructFromXml(sourceXml);
        };
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * constructFromXML method is used to populate the ConfigDataObject with source XML element
     *
     * @param   sourceXML               the source XML of config file.
     */
    private constructFromXml(sourceXml: Element): void {

        const children: Array<Element> = XmlParser.getChildElements(sourceXml);

        for (const child of children ) {
            switch (child.tagName) {
            case 'EnableBreadcrumb':
                this.enableBreadcrumb = (XmlParser.getTextContent(child) === 'true');
                break;
            case 'LockProgress':
                this.lockProgress = (XmlParser.getTextContent(child) === 'true');
                break;
            case 'filter':
                this.filter = XmlParser.getTextContent(child);
                break;
            case 'LogLevel':
                this.logLevel = <LogLevel>LogLevel[XmlParser.getTextContent(child)];
                break;
            case 'MaxCourseMapLevel':
                this.maxCourseMapLevel = XmlParser.getTextContent(child);
                break;
            case 'PageNumberLevel':
                this.pageNumberLevel = XmlParser.getTextContent(child);
                break;
            case 'PlayerWidth':
                this.playerWidth = +XmlParser.getTextContent(child);
                break;
            case 'PlayerHeight':
                this.playerHeight = +XmlParser.getTextContent(child);
                break;
            case 'PlayerCode':
                this.playerCode = XmlParser.getTextContent(child);
                break;
            }
        };

    }

}



