import { XmlParser } from '../transactions/xml-parser';

/**
 * The purpose of this class is to hold data used to describe a reference file.
 */
export class ReferenceDataObject {

    ///////////////////////////////////////////////////////////////
    ////////// Instance Variables
    ///////////////////////////////////////////////////////////////

    /**
     * Instance of the logger - if needed
     */
    // private logger: Logger = Log.getInstance().getLogger('Reference Data Object');

    /**
     * This is a unique identifier for the reference.
     */
    private _referenceID: string;

    /**
     * This is the name of the reference as it's displayed in the interface.
     */
    private _referenceName: string;

    /**
     * This is the actual name of the reference file as opposed to referenceName which is the name displayed.
     */
    private _originalFileName: string;

    /**
     * This is the URL used to resolve the location of the "original file". This will be the same as
     * the referenceURL unless processing was done to make these different. For instance, if the user uploads
     * a file that will be converted on the backend. This would be the link to that original files where as
     * referenceURL would be the file that we display.
     */
    private _originalFileURL: string;

    /**
     * This is used to distinguish the type of reference file that this reference represents.
     * i.e. File, URL, Glossary, AudioScript
     */
    private _referenceType: string;

    /**
     * This is the URL to the reference file that will be used for display
     */
    private _referenceURL: string;

    /**
     * This contains the binary data for the reference. It will only have data
     * when the user is attempting to add a reference.
     * Reserved for the future use
     */
    private _binaryData: any[]; // ByteArray;

    /**
     * This holds the UnitID of the reference
     */
    private _unitID: string;

    //////////////////////////////////////////////////////////////////
    ////////// Constructor
    //////////////////////////////////////////////////////////////////

    /**
     * Constructor for ReferenceDataObject class.
     */
    constructor () {
    //
    }


    //////////////////////////////////////////////////////////////////
    ////////// Getters / Setters
    //////////////////////////////////////////////////////////////////

    /**
     * This is the URL used to resolve the location of the "original file". This will be the same as
     * the referenceURL unless processing was done to make these different. For instance, if the user uploads
     * a file that will be converted on the backend. This would be the link to that original files where as
     * referenceURL would be the file that we display.
     */
    public get originalFileURL(): string {
        return this._originalFileURL;
    }

    /**
     * @private
     */
    public set originalFileURL(value: string) {
        this._originalFileURL = value;
    }

    /**
     * This holds the UnitID of the reference
     */
    public get unitID(): string {
        return this._unitID;
    }

    /**
     * @private
     */
    public set unitID(value: string) {
        this._unitID = value;
    }

    /**
     * This contains the binary data for the reference.
     * It will only have data when the user is attempting to add a reference.
     */
    public get binaryData(): any[] {
        return this._binaryData;
    }

    /**
     * @private
     */
    public set binaryData(value: any[]) {
        this._binaryData = value;
    }

    /**
     * This is the URL to the reference file that will be used for display
     */
    public get referenceURL(): string {
        return this._referenceURL;
    }

    /**
     * @private
     */
    public set referenceURL(value: string) {
        this._referenceURL = value;
    }

    ////////////////////////////////////////////////////////////////////////////////

    /**
     * This is used to distinguish the type of reference file that this reference represents.
     * i.e. File, URL, Glossary, AudioScript
     */
    public get referenceType(): string {
        return this._referenceType;
    }

    /**
     * @private
     */
    public set referenceType(value: string) {
        this._referenceType = value;
    }

    ////////////////////////////////////////////////////////////////////////////////

    /**
     * This is the actual name of the reference file as opposed to referenceName which is the name displayed.
     */
    public get originalFileName(): string {
        return this._originalFileName;
    }

    /**
     * @private
     */
    public set originalFileName(value: string) {
        this._originalFileName = value;
    }

    ////////////////////////////////////////////////////////////////////////////////

    /**
     * This is the name of the reference as it's displayed in the interface.
     */
    public get referenceName(): string {
        return this._referenceName;
    }

    /**
     * @private
     */
    public set referenceName(value: string) {
        this._referenceName = value;
    }

    ////////////////////////////////////////////////////////////////////////////////

    /**
     * This is a unique identifier for the reference.
     */
    public get referenceID(): string {
        return this._referenceID;
    }

    /**
     * @private
     */
    public set referenceID(value: string) {
        this._referenceID = value;
    }


    /////////////////////////////////////////////////////////////
    ////////// XML Handling
    /////////////////////////////////////////////////////////////

    /**
     * This function converts the reference data that is needed for server requests into XML (Element)
     *
     * @return	Element		an XML representation of the reference needed data to make a server request
     */
    public get referenceXML(): Element {
        // this creates a node called <Reference>
        const outReferenceXML: Element = XmlParser.xmlStringToElement('<Reference></Reference>');

        // only add an Reference ID node if there is an ID. When making a request to add a
        // reference the reference has no ID, to delete one, we need an ID
        if (this.referenceID) {
            outReferenceXML.appendChild(
                XmlParser.xmlStringToElement('<RefID>{this.referenceID}</RefID>')
            );

            outReferenceXML.appendChild(
                XmlParser.xmlStringToElement('<ReferenceName>{this.referenceName}</ReferenceName>')
            );

            outReferenceXML.appendChild(
                XmlParser.xmlStringToElement('<OriginalFileName>{this.originalFileName}</OriginalFileName>')
            );

            outReferenceXML.appendChild(
                XmlParser.xmlStringToElement('<ReferenceType>{this.referenceType}</ReferenceType>')
            );

            if (this.referenceURL) {
                outReferenceXML.appendChild(
                    XmlParser.xmlStringToElement('<RefURL>{this.referenceURL}</RefURL>')
                );
            }

            outReferenceXML.appendChild(
                XmlParser.xmlStringToElement('<UnitID>{ this.unitID } < /UnitID>')
            );

            return outReferenceXML;
        }
    }

    /**
     * This function initializes the data of a variable that is created in the reponse of a server transaction
     */
    public initializeDataFromServer(referenceXML: Element): void {
        this.referenceID = XmlParser.findDescendantTextContentByName(referenceXML, 'RefID');
        this.referenceName = XmlParser.findDescendantTextContentByName(referenceXML, 'ReferenceName');
        this.originalFileName = XmlParser.findDescendantTextContentByName(referenceXML, 'OriginalFileName');
        this.originalFileURL = XmlParser.findDescendantTextContentByName(referenceXML, 'OriginalFileURL');
        this.referenceType = XmlParser.findDescendantTextContentByName(referenceXML, 'ReferenceType');
        this.referenceURL = XmlParser.findDescendantTextContentByName(referenceXML, 'RefURL');
    }

    ///////////////////////////////////////////////////
    ////////// Helper functions
    ///////////////////////////////////////////////////

    /**
     * This function converts Reference Data Object to string for logging
     *
     * @param tab - "tab" size to indent the output string if needed
     * @default = '' (no indent)
     */
    public toString(tab: string = ''): string {
        let outString: string = '';

        outString += tab + 'referenceID = ' + this._referenceID + '\n';
        outString += tab + 'referenceName = ' + this._referenceName + '\n';
        outString += tab + 'referenceType = ' + this._referenceType + '\n';
        outString += tab + 'referenceType = ' + this._referenceType + '\n';
        outString += tab + 'referenceURL = ' + this._referenceURL + '\n';
        outString += tab + 'originalFileName = ' + this._originalFileName + '\n';
        outString += tab + 'originalFileURL = ' + this._originalFileURL + '\n';
        outString += tab + 'unitID = ' + this._unitID + '\n';

        return outString;
    }
}



