import { CheckboxDataObject } from '../../../shared/system-shared/model/data-objects/components/checkbox.data-object';
import { Directive } from '@angular/core';
import { EventDataObject } from '../../../shared/system-shared/model/data-objects/event-system/event.data-object';
import { EventHandlerDirective } from './event-handler.directive';
import { Log, Logger } from '../../../shared/system-shared/log';


@Directive({
  selector: '[playerCheckboxEventHandler]'
})

export class CheckboxEventHandlerDirective extends EventHandlerDirective {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Parse event triggers
     *
     * overrides base version
     * @param   eventType   DIF defined event type, for example, OnClick, OnDoubleClick...
     */
    protected parseEventTriggers(eventType: string): void {
        super.parseEventTriggers(eventType);
        switch (eventType) {
            case EventDataObject.ON_CHECKED:
                this.createEventListener('change', eventType);
                break;
            case EventDataObject.ON_UNCHECKED:
                this.createEventListener('change', eventType);
                break;
        }
    }

    /**
     * Process event
     *
     * overrides base version
     * @param   eventType   DIF defined event type, for example, OnClick, OnDoubleClick...
     */
    protected processEvent(event: Event , eventType: string): void {

        if (eventType === EventDataObject.ON_CHECKED || eventType === EventDataObject.ON_UNCHECKED) {
            this.updateCheckedInDataSource(event);
        } else {
            super.processEvent(event, eventType);
        }

    }

    /**
     * Overrides base.  Adds call to updateCheckedInDataSource.
     *
     * @inheritDoc
     */
    protected processAction(event: Event): void {
        console.log('!!! CheckboxDirective - processAction triggered !!!');
        // NOTE: if Action and Event could run this method twice
        // TODO: check to make sure this doesn't cause any bugs, otherwise will have to deal with it
        this.updateCheckedInDataSource(event);
        super.processAction(event);
    }

    /**
     * Updates checked property in CheckboxDataObject to match state of component / display object
     * TODO: Jay says the intention of this is to allow Event System to trigger the change event on the component to
     *          trigger a further Event System ON_CHECKED or ON_UNCHECKED, and any Action System validation processes.
     *          Not sure at this point whether this behavior is desirable (Flash Player doesn't do this).
     */
    private updateCheckedInDataSource(event: Event): void {
        (this.dataSource as CheckboxDataObject).checked = (event.currentTarget as HTMLInputElement).checked;
    }

}



