import { SetPropertyDataObject }
from '../../../../../shared/system-shared/model/data-objects/event-system/functions/set-property.data-object';
import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { InjectorService } from '../../../../../shared/system-shared/model/services/injector.service';
import { ValuePlayableDataObject } from '../value.playable.data-object';
import { PageService } from '../../../../../shared/system-shared/model/services/page.service';
import { ComponentDataObject }
from '../../../../../shared/system-shared/model/data-objects/components/component.data-object';
import { FunctionPlayableDataObjectInterface } from './function.playable.data-object.interface';
import { FunctionPlayableDataObjectCollection } from '../function.playable.data-object.collection';



/**
 * Playable version of SetProperty data object
 */
export class SetPropertyPlayableDataObject extends SetPropertyDataObject
    implements FunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableSetPropertyLogger: Logger;

    private pageService: PageService = InjectorService.injector.get(PageService);

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableSetPropertyLogger = Log.getInstance().getLogger('SetPropertyPlayableDataObject');
        this.playableSetPropertyLogger.debug('constructorPreprocessing(): Playable SetProperty');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * overrides base version
     * @param sourceElement
     */
    protected createValueDataObject(sourceElement: ElementDataObject): void {
        this._value =  new ValuePlayableDataObject(sourceElement);
        this._value.parent = this;
    }




    /**
     * <p>Run SetProperty.</p>
     *
     */
    public runFunction(): void {
        // Get value from ValueDataObject
        const value: any = (this._value as ValuePlayableDataObject).returnValue;

        // Call method to set property
        this.setProperty(value);

        // send completion to parent
        (this._parent as FunctionPlayableDataObjectCollection).childProcessComplete();

    }

	/**
	 * <p>Function that sets the property's value.</p>
	 */
    private setProperty(value: any): void {
        if (!this.componentElementID || !this.propertyName) {
            this.playableSetPropertyLogger.debug
            ('runFunction(): Error Missing required Data: ComponentElementID and PropertyName');
            return;
        }
        const component: ComponentDataObject = this.pageService.getComponent(+this.componentElementID);
        if (!component) {
            this.playableSetPropertyLogger.debug
            ('ERROR: runFunction(): component not found - ' + this.componentElementID);
            return;
        }
        if (!component.getPropertyMap(this.propertyName)) {
            this.playableSetPropertyLogger.debug('ERROR: runFunction(): property not found - ' + this.propertyName);
        } else {
            this.playableSetPropertyLogger.debug
            ('Vaule Before Set = ' + component[component.getPropertyMap(this.propertyName).publicName]);
            component[component.getPropertyMap(this.propertyName).publicName] = value;
            this.playableSetPropertyLogger.debug
            ('Vaule After Set = ' + component[component.getPropertyMap(this.propertyName).publicName]);
        }
    }


}



