import { Observable }   from 'rxjs/Observable';

import { Transaction }  from './transaction';

import { LoadConfigTaskHandler }    from './transaction-task-handlers/load-config.task-handler';


/**
 * This class is responsible for loading configuration data from file.
 *
 * @see Transaction
 * @see TransactionDataSource
 */
export class LoadConfigTransaction extends Transaction {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new LoadConfigTransaction instance.
     */
    constructor() {

        super('LoadConfig');
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * @inheritDoc
     */
    public startTransaction(): Observable<any> {

        // add TaskHandler for loading config data
        const taskHandler: LoadConfigTaskHandler = new LoadConfigTaskHandler();
        taskHandler.buildRequestXml();
        this.addTaskHandler(taskHandler);

        // Send request to get processed
        return super.startTransaction();
    }
}



