import { BrowserAnimationsModule }  from '@angular/platform-browser/animations';
import { CommonModule }             from '@angular/common';
import { FormsModule }              from '@angular/forms';
import { HttpModule }               from '@angular/http';
import { NgModule }                 from '@angular/core';
import { MdButtonModule,
         MdIconModule }             from '@angular/material';

import { AudioEventHandlerDirective }           from './view/directive/audio-event-handler.directive';
import { CheckboxEventHandlerDirective }        from './view/directive/checkbox-event-handler.directive';
import { DropdownListEventHandlerDirective }    from './view/directive/dropdown-list-event-handler.directive';
import { EventHandlerDirective }                from './view/directive/event-handler.directive';
import { RadioButtonEventHandlerDirective }     from './view/directive/radio-button-event-handler.directive';
import { TextEventHandlerDirective }            from './view/directive/text-event-handler.directive';
import { VideoEventHandlerDirective }           from './view/directive/video-event-handler.directive';

import { AudioComponent }               from './view/audio.component';
import { ButtonComponent }              from './view/button.component';
import { CheckboxComponent }            from './view/checkbox.component';
import { ContentWindowComponent }       from './view/content-window.component';
import { DropdownListComponent }        from './view/dropdown-list.component';
import { HtmlComponent }                from './view/html.component';
import { ImageComponent }               from './view/image.component';
import { MenuComponent }                from './view/menu.component';
import { PageComponent }                from './view/page.component';
import { PlayerComponent }              from './view/player.component';
import { QuizFillInBlankComponent }     from './view/quiz-fill-in-blank.component';
import { QuizMatchComponent }           from './view/quiz-match.component';
import { QuizMultipleChoiceComponent }  from './view/quiz-multiple-choice.component';
import { QuizResultComponent }          from './view/quiz-result.component';
import { RadioButtonComponent }         from './view/radio-button.component';
import { TextDisplayComponent }         from './view/text-display.component';
import { TextInputComponent }           from './view/text-input.component';
import { TextInputAreaComponent }       from './view/text-input-area.component';
import { VideoComponent }               from './view/video.component';


@NgModule({
    imports: [ BrowserAnimationsModule, CommonModule, FormsModule, HttpModule, MdButtonModule, MdIconModule ],
    declarations: [
        AudioEventHandlerDirective,
        CheckboxEventHandlerDirective,
        DropdownListEventHandlerDirective,
        EventHandlerDirective,
        RadioButtonEventHandlerDirective,
        TextEventHandlerDirective,
        VideoEventHandlerDirective,
        AudioComponent,
        ButtonComponent,
        CheckboxComponent,
        ContentWindowComponent,
        DropdownListComponent,
        HtmlComponent,
        ImageComponent,
        MenuComponent,
        PageComponent,
        PlayerComponent,
        QuizFillInBlankComponent,
        QuizMatchComponent,
        QuizMultipleChoiceComponent,
        QuizResultComponent,
        RadioButtonComponent,
        TextDisplayComponent,
        TextInputComponent,
        TextInputAreaComponent,
        VideoComponent,
    ],
    exports: [ PlayerComponent ]
})

/**
 * Player module - contains view and model objects associated with Player
 *
 * TODO: might want to break this down into smaller player related modules (initialization?, player chrome?, player
 * TODO: content area?) in which case this would maybe be the module that takes handles the page content? Yes :) - PV
 * TODO: and then after renaming this module replace that useless description up above :P
 */
export class PlayerModule { }



