import { ElementBasedDataObject } from '../element-based.data-object';
import { ElementDataObject } from '../element.data-object';
import { EventDataObjectCollection } from './event.data-object.collection';
import { EventHandlerDataObject } from './event-handler.data-object';
import { EventSystemDataObjectInterface } from './event-system.data-object.interface';
import { Log, Logger } from '../../../log';


/**
 * Event System data object class that defines an Event, including what triggers it and what it should do when it gets
 * triggered.  (i.e. what should happen when a button is clicked, a variable changes, an event completes, etc.)
 *
 * TODO: AS version implements ITreeNodeData interface - may need that eventually
 */
export abstract class EventDataObject extends ElementBasedDataObject implements EventSystemDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * String used to identify Element associated with an Event (via ElementDataObject.name)
     * @type {string}
     */
    public static readonly EVENT_ELEMENT_NAME: string = 'Event';

    /**
     * String used to identify Element identifying this Event's Trigger
     * @type {string}
     */
    protected static readonly TRIGGER_ELEMENT_NAME: string = 'Trigger';

    /**
     * Constants for all possible values of trigger property
     * @type {string}
     */
    public static readonly ON_CHANGE: string = 'OnChange';
    public static readonly ON_CHECKED: string = 'OnChecked';
    public static readonly ON_CLICK: string = 'OnClick';
    public static readonly ON_COMPLETE: string = 'OnComplete';
    public static readonly ON_DOUBLE_CLICK: string = 'OnDoubleClick';
    public static readonly ON_EXIT: string = 'OnExit';
    public static readonly ON_FOCUS: string = 'OnFocus';
    public static readonly ON_FOCUS_LOST: string = 'OnFocusLost';
    public static readonly ON_LOAD: string = 'OnLoad';
    public static readonly ON_MOUSE_OUT: string = 'OnMouseOut';
    public static readonly ON_MOUSE_OVER: string = 'OnMouseOver';
    public static readonly ON_PAUSE: string = 'OnPause';
    public static readonly ON_PLAY: string = 'OnPlay';
    public static readonly ON_SELECTED: string = 'OnSelected';
    public static readonly ON_SELECTION_CHANGE: string = 'OnSelectionChange';
    public static readonly ON_UNSELECTED: string = 'OnUnselected';
    public static readonly ON_UNCHECKED: string = 'OnUnchecked';



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Parent Event Collection
     */
    protected _parent: EventDataObjectCollection;

    /**
     * Collection of EventHandlerDataObject which define the things that need to happen when the event is triggered
     */
    public eventHandlerCollection: Array<EventHandlerDataObject>;

    /**
     * ElementDataObject which contains data specifying the action that triggers this event (i.e. onMouseClick,
     * onVariableChange, etc.).  Used to determine type of listener / event handler to add to the EventContainer
     * (i.e. components, variables, other events).  The value of the Element object (but not the object itself) is used
     * outside of this class and accessed through an accessor method.
     */
    protected triggerElement: ElementDataObject;

    /**
     * Logger instance for logging
     */
    private eventLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor.  Based on the ElementBasedDataObject class that this extends from, sourceElement is passed to the
     * constructFromElement method to complete construction of this object.  The data in sourceElement define the
     * properties of this Event and its collection of Handlers.
     *
     * NOTE: At this point, passing an empty sourceElement will result in an object that's largely incompatible with
     *          most of the rest of the system without adding special processing, and should generally not be done.
     *          Designers that need to create new versions of this object should use a derived, designer specific,
     *          editable version of this class that provides the additional functionality of setting up a new empty
     *          object by overriding the base constructNew method.
     *
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.eventLogger = Log.getInstance().getLogger('EventDataObject');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
        this.eventHandlerCollection = [];
    }

    /**
     * Overrides base version.
     * Performs Event object specific condition testing on the children of _localRoot to identify and setup
     * class specific properties including the Event Handlers associated with it.
     *
     * @param  element -> Element to be tested (typically by checking it's name property)
     */
    protected parseElement(element: ElementDataObject): void {
        switch (element.name) {
            case EventDataObject.TRIGGER_ELEMENT_NAME:
                this.triggerElement = element;
                this.eventLogger.debug('constructFromElement(): Trigger = ' + this.trigger);
                break;
            case EventHandlerDataObject.EVENT_HANDLER_ELEMENT_NAME:
                // Create Event Handler and add to eventHandlerCollection
                this.addEventHandler(this.createEventHandler(element));
                break;
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    public set parent(value: EventDataObjectCollection) {
        this._parent = value;
    }

    /**
     * Accessor for value of triggerElement
     * @returns {string}
     */
    public get trigger(): string {
        return this.triggerElement.value;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Should be overridden in playable and editable versions of this class to return the appropriate version of
     * EventHandlerDataObject
     *
     * @param sourceElement -> root level Element to construct an Event Handler from.
     */
    protected createEventHandler(sourceElement: ElementDataObject): EventHandlerDataObject {
        /*
         Since this class is abstract, this version of createEventHandler should never actually be called so will
         return undefined so that base classes can remain abstract
         */
        return undefined;
    }

    /**
     * Add EventHandlerDataObject to eventHandlerCollection array
     * @param eventHandler
     */
    protected addEventHandler(eventHandler: EventHandlerDataObject): void {
        // Add to array
        this.eventHandlerCollection.push(eventHandler);

        // Set parent
        eventHandler.parent = this;
    }

}



