import { Component }    from '@angular/core';

import { AbstractComponent }    from './abstract.component';
import { CheckboxDataObject }   from '../../shared/system-shared/model/data-objects/components/checkbox.data-object';


@Component({
    selector: 'player-checkbox',
    templateUrl: 'checkbox.component.html',
    styleUrls:  [ '../../app.component.css' ]
})


/**
 * The view model of the application's Angular checkbox component.
 */
export class CheckboxComponent extends AbstractComponent {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * A convenience reference to the dataSource that provides the model data for this component cast as an actual
     * CheckboxDataObject.
     *
     * @default undefined
     */
    private checkbox: CheckboxDataObject;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The style Object used by the ngStyle directive that updates the inline style attribute of the input tag used in
     * this component's template.
     *
     * @default A style Object containing the CSS properties for this component's input tag.
     */
    public get inputTagStyle(): Object {

        return this.commonStyleProperties;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Angular Lifecycle Hooks
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * @inheritDoc
     */
    public ngOnInit(): void {

        super.ngOnInit();

        this.checkbox = this.dataSource as CheckboxDataObject;

    }
}



