import { Log, Logger } from '../../shared/system-shared/log';
import { XmlParser } from '../../shared/system-shared/model/transactions/xml-parser';

/**
 * data object holder for offline IMIManifest.xml file
 *
 */
export class ManifestDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
  
    /**
     * An instance of the logger class for outputting log messages.
     */
    // private logger: Logger = Log.getInstance().getLogger('ManifestDataObject');
  
  
    public courseDir: string;
    public courseTOC: string;
    public courseUnitType: string;
    public pageDir: string;
    public referenceDir: string;
    public courseVariables: string;
    public pages: string;
    public content: string;

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new ManifestDataObject instance.
     *
     * @param   sourceXml
     */
    public constructor(sourceXml: Element) {
        this.constructFromXml(sourceXml);
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    
    /**
     * constructFromXML method is used to populate the ManifestDataObject with source XML element
     *
     * @param   sourceXML               the source XML of manifest file.
     */
    private constructFromXml(sourceXml: Element): void {
        let children: Array<Element> = XmlParser.getChildElements(sourceXml);

        for ( let child of children ) {
            switch (child.tagName) {
            case 'Pages':
                this.pages = XmlParser.getTextContent(child);
                break;
            case 'Content':
                this.content = XmlParser.getTextContent(child);
                break;
            case 'Course':
                let courseInfos: Array<Element> = XmlParser.getChildElements(child);
                for ( let info of courseInfos ) {
                    switch (info.tagName) {
                    case 'CourseDir':
                        this.courseDir = XmlParser.getTextContent(info);
                        break;
                        case 'CourseTOC':
                            this.courseTOC = XmlParser.getTextContent(info);
                            break;
                        case 'CourseUnitType':
                            this.courseUnitType = XmlParser.getTextContent(info);
                            break;
                        case 'PageDir':
                            this.pageDir = XmlParser.getTextContent(info);
                            break;
                        case 'ReferenceDir':
                            this.referenceDir = XmlParser.getTextContent(info);
                            break;
                        case 'Variables':
                            this.courseVariables = XmlParser.getTextContent(info);
                            break;
                    }
                };
                break;
            }
        };
    }

}



