import { LMSConstant } from './lms.constant';
/*
 *  Objective class for supported cmi.objectives data model
 */

export class Objective {

        private _objectiveID: number;

        private _objectiveName: string;

        private _objectiveDesc: string;

        private _score_min: number;

        private _score_max: number;

    /**
     * The cmi.objectives.n.score.scaled data model element is a number that reflects the performance of the learner
     * for the objective. The value of the data model element is scaled to fit the range -1 to 1 inclusive
     */
        private _score_scaled: number; 	

    /**
     * The cmi.objectives.n.score.raw data model element is a number that reflects the performance of the learner,
     * for the objective, relative to the range bounded by the values of min and max
     *
     */
        private _score_raw: number;		

        private _success_status: string;

        private _completion_status: string;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new Objective instance.
     *
     * @param
     */

    constructor() {
			
        this._objectiveID = 0;
        this._objectiveDesc = '';
        this._objectiveName = '';
        this._score_min = 0;
        this._score_max = 100;

        // We do not use cmi.objectives.n.success_status 
        // instead we use cmi.success_status
        // this._success_status = LMSConstant.UNKNOWN;

        this._completion_status = LMSConstant.INCOMPLETE;

    }


    /**
     * The unique objectiveID
     *
     * @default undefined
     */
    public get objectiveID(): number {

        return this._objectiveID;
    }

    public set objectiveID(value: number) {

        this._objectiveID = value;
    }
      
    public get objectiveName(): string {

        return this._objectiveName;
    }

    public set objectiveName(value: string) {

            this._objectiveName = value;
    }
      
    public get objectiveDesc(): string {

        return this._objectiveDesc;
    }

    public set objectiveDesc(value: string) {

        this._objectiveDesc = value;
    }

    public set scoreRaw(value: number) {

        this._score_raw = value;

        // score_raw/100 * 2 - 1
        this._score_scaled = +((value / 50) - 1).toFixed(2);
    }

    public get scoreRaw(): number {

        return this._score_raw;
    }


    public get scoreScaled(): number {

        return this._score_scaled;
    }

    public set scoreScaled(value: number) {

        this._score_scaled = value;

    }

    public get scoreMin(): number {

        return this._score_min;

    }

    public set scoreMin(value: number) {

        this._score_min = value;
    }

    public get scoreMax(): number {

        return this._score_max;
    }

    public set scoreMax(value: number) {

        this._score_max = value;
    }
           

    public get successStatus(): string {

        return this._success_status;
    }

    public set successStatus(value: string) {

        this._success_status = value;
    }

      
    public get completionStatus(): string {

        return this._completion_status;
    }

    public set completionStatus(value: string) {

        this._completion_status = value;
    }
      

}



