/* tslint:disable:max-line-length */
import { EventEmitter } from '@angular/core';

import { ColorDataType }        from '../data-types/color.data-type';
import { MarkupTextDataType }   from '../data-types/markup-text.data-type';

import { ComponentContainerDataObject }         from './component-container.data-object';
import { ElementBasedDataObject }               from '../element-based.data-object';
import { ElementDataObject }                    from '../element.data-object';
import { EventDataObjectCollection }            from '../event-system/event.data-object.collection';
import { EventPlayableDataObjectCollection }    from '../../../../../player/model/data-objects/event-system/event.playable.data-object.collection';
import { FrameworkActionDataObject }            from '../action-system/framework-action.data-object';
import { FrameworkActionPlayableDataObject }    from '../../../../../player/model/data-objects/action-system/framework-action.playable.data-object';

import { EventContainerDataObjectInterface }    from '../event-system/event-container.data-object.interface';

import { AbstractComponent }    from '../../../../../player/view/abstract.component';

import { Log, Logger }  from '../../../log';
/* tslint:enable:max-line-length */


/*
 *
 *  Base class for all DIF components (Page, ContentWindow, Button, Image, etc)
 *
 * TODO: re-comment this class
 *
 * TODO: change the private instance properties to be ElementDataObjects
 */
export abstract class ComponentDataObject extends ElementBasedDataObject implements EventContainerDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    public id: number;

    private _x: number;
    private _y: number;
    private _z: number;

    private _width: number;
    private _height: number;
    private _name: string;
    private _visible: boolean;

    /**
     * The Element that contains the alpha property of this component
     */
    private _alphaElement: ElementDataObject;

    /**
     * The focus property(state) of this component
     */
    private _focus: boolean = false;

    /**
     * The mouse over property(state) of this component
     */
    private _mouseOver: boolean = false;


    /**
     * See accessor
     */
    protected _eventCollection: EventDataObjectCollection;

    /**
     * See accessor
     */
    protected _frameworkAction: FrameworkActionDataObject;

    /**
     * See accessor
     */
    private _viewComponent: AbstractComponent;

    protected initPropertyMapArray: Array<Object>;

    protected propertyMaps: Array<PropertyMap>;

    public parent: ComponentContainerDataObject;

    /**
     * When a component(like audio,video) completed playing, emit this onComplete event
     */
    public onComplete: EventEmitter<any> = new EventEmitter();        // TODO: might not want to init this here.

    /**
     * Reference to logger object for logging.
     */
    private componentDataObjectLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Abstract Constructor - provides implementation common to all component data objects.
     *
     * @param   sourceElement   The root of the ElementDataObject hierarchy which contains the source data we will be
     *                          constructed from.
     */
    constructor(sourceElement: ElementDataObject) {

        super(sourceElement);

        this.id = this.localRoot.id;
    }

    /**
     * This method is called from the base constructor.
     * It is used for initializations (logger only for now).
     */
    protected constructorPreprocessing(): void {
        this.componentDataObjectLogger = Log.getInstance().getLogger('ComponentDataObject');
        super.constructorPreprocessing();

        this.initializePropertyNames();
        this.propertyMaps = [];
        this.constructPropertyMaps();
    }

    /**
     * Initialize properties by adding all common properties to initPropertyMapArray
     * overridden and extended by derived class to add component's specific properties
     */
    protected initializePropertyNames(): void {
        this.initPropertyMapArray = [
            ['_x', 'x', 'X', 'number'],
            ['_y', 'y', 'Y', 'number'],
            ['_z', 'z', 'Z', 'number'],
            ['_width', 'width', 'Width', 'number'],
            ['_height', 'height', 'Height', 'number'],
            ['_name', 'name', 'Name', 'string'],
            ['_visible', 'visible', 'Visible', 'boolean'],
            ['_focusElement', 'focus', 'Focus', 'element'],
            ['_mouseOverElement', 'mouseOver', 'MouseOver', 'element'],
            ['_alphaElement', 'alpha', 'Alpha', 'element'],
            ['_alphaElement', 'alpha', 'alpha', 'element']  // added to address Designer setting "alpha" in event data
        ];

    }

    /**
     * Create and add new property map to propertyMaps from initPropertyMapArray.
     */
    protected constructPropertyMaps(): void {
        for (const p of this.initPropertyMapArray) {
            const map: PropertyMap = new PropertyMap();
            map.privateName = p[0];
            map.publicName = p[1];
            map.dBName = p[2];
            map.type = p[3];
            this.propertyMaps.push(map);
        }
    }

    /**
     * Set property initial value.
     *
     * @param child
     */
    protected setPropertyInitValue(child: ElementDataObject): void {
        const propertyMap: PropertyMap = this.getPropertyMap(child.name);
        if (!propertyMap) {
            return;
        }
        const type: string = propertyMap.type;
        const privateName: string = propertyMap.privateName;
        switch (type) {
            case 'element':
                this[privateName] = child;
                break;
            case 'string':
                this[privateName] = child.value;
                break;
            case 'number':
                this[privateName] = +child.value;
                break;
            case 'boolean':
                this[privateName] = (child.value === 'true');
                break;
            case 'color':
                this[privateName] = new ColorDataType(child);
                break;
            case 'text':
                this[privateName] = new MarkupTextDataType(child);
                break;
        }
    }

    /**
     * Parse base component properties.
     * @param child
     */
    protected parseElement(child: ElementDataObject): void {
        this.parseProperties(child);
    }

    /**
     * Properties parser.
     */
    protected parseProperties(element: ElementDataObject): void {
        if (element.name === EventDataObjectCollection.EVENT_COLLECTION_ELEMENT_NAME) {
            this.eventCollection = this.createEventCollection(element);
            this.eventCollection.parent = this;
        } else if (element.name === FrameworkActionDataObject.FRAMEWORK_ACTION_ELEMENT_NAME) {
            this.componentDataObjectLogger.debug('parseProperties(): FrameworkAction');
            this._frameworkAction = this.createFrameworkAction(element);
            // TODO: do we need to create a parent reference in FrameworkAction?
        } else {
            this.setPropertyInitValue(element);
        }

    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    public get x(): number {
        return this._x;
    }

    public set x(value: number) {
        this._x = value;
    }

    ////////////////////////////////////////////////////////////////

    public get y(): number {
        return this._y;
    }

    public set y(value: number) {
        this._y = value;
    }

    ////////////////////////////////////////////////////////////////

    public get z(): number {
        return this._z;
    }

    public set z(value: number) {
        this._z = value;
    }

    ////////////////////////////////////////////////////////////////

    public get width(): number {
        return this._width;
    }

    public set width(value: number) {
        this._width = value;
    }

    ////////////////////////////////////////////////////////////////

    public get height(): number {
        return this._height;
    }

    public set height(value: number) {
        this._height = value;
    }

    ////////////////////////////////////////////////////////////////

    public get name(): string {
        return this._name;
    }

    public set name(value: string) {
        this._name = value;
    }

    ////////////////////////////////////////////////////////////////

    public get visible(): boolean {
        return this._visible;
    }

    public set visible(value: boolean) {
        this._visible = value;
    }

    ////////////////////////////////////////////////////////////////

    /**
     * The alpha property of this component
     */
    public get alpha(): string {
        return this._alphaElement.value;
    }

    public set alpha(value: string) {
        this._alphaElement.value = value;
    }

    /**
     * The focus property of this component
     */
    public get focus(): boolean {
        return (this._focus);
    }

    public set focus(value: boolean) {
        this._focus = value;
    }

    /**
     * The mouse over property of this component
     */
    public get mouseOver(): boolean {
        return (this._mouseOver);
    }

    public set mouseOver(value: boolean) {
        this._mouseOver = value;
    }


    ////////////////////////////////////////////////////////////////

    /**
     * Reference to component's display object
     *
     * @internal: Player view components at this point, may eventually need class factory to return version for
     *              Designers
     */
    public get viewComponent(): AbstractComponent {
        return this._viewComponent;
    }

    public set viewComponent(value: AbstractComponent) {
        this._viewComponent = value;
    }

    ////////////////////////////////////////////////////////////////

    /**
     * Reference to EventDataObjectCollection that holds all Event System data for the component
     */
    public get eventCollection(): EventDataObjectCollection {
        return this._eventCollection;
    }

    public set eventCollection(value: EventDataObjectCollection) {
        this._eventCollection = value;
    }

    ////////////////////////////////////////////////////////////////

    /**
     * Reference to FrameworkActionDataObject that holds data structure for old Action system
     */
    public get frameworkAction(): FrameworkActionDataObject {
        return this._frameworkAction;
    }

    /**
     * Get correct value in checkbox/radio button/text box/dropdown and it will pass to Framework Action
     * To be overridden by derived component
     */
    public get correctValue(): string {
        return undefined;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Creates and returns an EventDataObjectCollection
     * Should be overridden in editable versions of this class to return the editable version.
     *
     * @param element -> root level Element to construct EventDataObjectCollection from.
     * @returns {EventDataObjectCollection}
     */
    protected createEventCollection(element: ElementDataObject): EventDataObjectCollection {
        // TODO: Should return playable version once created - there will be no playable version ComponentDataObject
        // TODO: Can't reference playable (Player class) from shared ... need to find approach to fix this
        // return new EventDataObjectCollection(element);
        return new EventPlayableDataObjectCollection(element);
    }

    /**
     * Creates and returns FrameworkActionDataObject
     *
     * @param element -> root level Element to construct FrameworkAction from
     * @returns {FrameworkActionDataObject}
     */
    protected createFrameworkAction(element: ElementDataObject): FrameworkActionPlayableDataObject {
        // TODO: Should return playable version once created - which should be done via a class factory once implemented
        const action: FrameworkActionPlayableDataObject = new FrameworkActionPlayableDataObject(element);
        action.parent = this;
        return action;
    }

    /**
     * To be overridden in derived classes
     */
    public validate(): boolean {
        return undefined;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Helper functions
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Concatenate class properties into a string
     */
    public serializeToString(): string {
        let outString: string = 'Component Data Object:\n';

        outString += 'height = ' + this.height + '\n';
        outString += 'id = ' + this.id + '\n';
        outString += 'name = ' + this.name + '\n';
        outString += 'visible = ' + this.visible + '\n';
        outString += 'width = ' + this.width + '\n';
        outString += 'x = ' + this.x + '\n';
        outString += 'y = ' + this.y + '\n';
        outString += 'z = ' + this.z + '\n';
        outString += 'localRoot = ' + this.localRoot.name;

        return outString;
    }

    /**
     * Get PropertyMap of a component's property by DB name.
     *
     * @param   name    Property DB name.
     *
     * @returns If found, the property map with the given DB name, otherwise undefined.
     */
    public getPropertyMap(name: string): PropertyMap {
        if (!this.propertyMaps) {
            return undefined;
        } else {
            return this.propertyMaps.find(p => p.dBName === name);
        }
    }

}


/**
 * A helper Class to contain the mapping of component property's private name, public name(getter/setter) and DB name
 */
class PropertyMap {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * A private(internal) name of a component, for example, _x,_y,_z,_width,_fontColor
     */
    public privateName: string;

    /**
     * A public getter/setter name of a component property, for example, x,y,z,width,fontColor
     */
    public publicName: string;

    /**
     * A Database-defined name of a component property, for example, X,Y,Z,Width,FontColor
     */
    public dBName: string;

    /**
     * The type of the property, for example, string,number,boolean
     */
    public type: string;


    /**
     * A helper Class to contain the mapping of component property's private name,
     * public name(getter/setter) and DB name
     */
    constructor() {
    }

}



