import { ElementBasedDataObject } from '../element-based.data-object';
import { ElementDataObject } from '../element.data-object';
import { EventContainerDataObjectInterface } from './event-container.data-object.interface';
import { EventDataObject } from './event.data-object';
import { EventSystemDataObjectInterface } from './event-system.data-object.interface';
import { Log, Logger } from '../../../log';


/**
 * Event System data object class that manages its collection Events.  It is the root data object for the Event System
 *
 * TODO: This implements ITreeNodeData in Flash code - may need to add it at some point.
 */
export abstract class EventDataObjectCollection extends ElementBasedDataObject
    implements EventSystemDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * String used to identify an Element associated with an Event Collection (via name)
     * @type {string}
     */
    public static readonly EVENT_COLLECTION_ELEMENT_NAME: string = 'Events';


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Parent Event Container object (typically a component, variable, or another event which should implement the
     * EventContainerDataObjectInterface)
     *
     * NOTE: Typescript has the ability to specify type like this (may want to try it instead of empty interface):
     *          protected _parent: ComponentDataObject | VariableDataObject | EventHandlerDataObject
     *
     * NOTE: Just about every Event System related object has a parent property which ties the object to its parent data
     *          object.  Many objects need this in order to pass execution control back up to its parent object.  Some
     *          don't really need it (parent usually holds reference to child data objects as well), but has generally
     *          been included for consistency.  There had been some thought to move this property to a base class like
     *          ElementBasedDataObject or a base class at the level of the EventSystemDataObjectInterface.  It was
     *          determined that many ElementBasedDataObjects (like all of the DIF components) don't need the ability for
     *          the child data object to access its parent, so it was decided not to add this property to it
     *          (particularly since the reference is generally redundant anyway and needs to be kept in sync).  Adding
     *          the property to a base class at the level of EventSystemDataObjectInterface would require generic
     *          typing of the property (since every Event System data object typically has different parent objects),
     *          which would then have to be cast in the derived class to strongly type it.  This isn't terrible if it
     *          allows consolidation of code, but in the case of the Event System data objects, currently there aren't
     *          any real shared processes that get applied to all parent objects regardless of their actual type.  So
     *          for now, each Event System data object is allowed to define and type its own parent property.  Can
     *          change this in the future if the situation changes.
     */
    protected _parent: EventContainerDataObjectInterface;

    /**
     * Event Collection
     */
    public eventCollection: Array<EventDataObject>;

    /**
     * Logger instance for logging
     */
    private eventCollectionLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor.  Based on the ElementBasedDataObject class that this extends from, sourceElement is passed to the
     * constructFromElement method to complete construction of this object.  The data in sourceElement defines the
     * collection of Events that this class manages.
     *
     * NOTE: At this point, passing an empty sourceElement will result in an object that's largely incompatible with
     *          most of the rest of the system without adding special processing, and should generally not be done.
     *          Designers that need to create new versions of this object should use a derived, designer specific,
     *          editable version of this class that provides the additional functionality of setting up a new empty
     *          object by overriding the base constructNew method.
     *
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.eventCollectionLogger = Log.getInstance().getLogger('EventDataObjectCollection');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
        this.eventCollection = [];
    }

    /**
     * Overrides base version.
     * Performs Event Collection specific condition testing on the children of _localRoot to identify and setup
     * class specific properties
     *
     * @param  element -> Element to be tested (typically by checking it's name property)
     */
    protected parseElement(element: ElementDataObject): void {
        switch (element.name) {
            case EventDataObject.EVENT_ELEMENT_NAME:
                // Create and add Event to collection
                this.addEvent(this.createEvent(element));
                break;
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    public set parent(value: EventContainerDataObjectInterface) {
        this._parent = value;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Should be overridden in playable and editable versions of this class to return the appropriate version of
     * EventDataObject
     *
     * @param sourceElement -> root level Element to construct an EventDataObject from.
     */
    protected createEvent(sourceElement: ElementDataObject): EventDataObject {
        /*
         Since this class is abstract, this version of createEvent should never actually be called so will
         return undefined so that base classes can remain abstract
         */
        return undefined;
    }

    /**
     * Adds Event to Event Collection and sets this as the Event's parent
     *
     * @param event
     */
    protected addEvent(event: EventDataObject): void {
        // Add to Event Collection
        this.eventCollection.push(event);

        // Set this as the parent of the Event added
        event.parent = this;
    }

    /**
     * Returns Event in eventCollection based on its trigger property
     *
     * @param trigger - the value that we want to match the trigger property with
     * @returns {undefined|EventDataObject}
     */
    public getEventByTrigger(trigger: string): EventDataObject {
        return this.eventCollection.find((event: EventDataObject) => {
            return event.trigger === trigger;
        });
    }
}



