import { AnimatePlayableDataObject } from './functions/animate.playable.data-object';
import { ChangePagePlayableDataObject } from './functions/change-page.playable.data-object';
import { DelayPlayableDataObject } from './functions/delay.playable.data-object';
import { DisplayMessagePlayableDataObject } from './functions/display-message.playable.data-object';
import { ElementDataObject } from '../../../../shared/system-shared/model/data-objects/element.data-object';
import { EventSystemDataObjectInterface }
    from '../../../../shared/system-shared/model/data-objects/event-system/event-system.data-object.interface';
import { FunctionDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/functions/function.data-object';
import { FunctionDataObjectCollection }
    from '../../../../shared/system-shared/model/data-objects/event-system/function.data-object.collection';
import { FunctionGroupPlayableDataObject } from './function-groups/function-group.playable.data-object';
import { FunctionPlayableDataObjectInterface } from './functions/function.playable.data-object.interface';
import { Log, Logger } from '../../../../shared/system-shared/log';
import { SetPropertyPlayableDataObject } from './functions/set-property.playable.data-object';
import { SetScormPlayableDataObject } from './functions/set-scorm.playable.data-object';
import { SetVariablePlayableDataObject } from './functions/set-variable.playable.data-object';
import { WaitPlayableDataObject } from './functions/wait.playable.data-object';


/**
 * Playable version of Function Collection
 */
export class FunctionPlayableDataObjectCollection extends FunctionDataObjectCollection {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private functionPlayableCollectionLogger: Logger;

    private functionsToRun: Array<FunctionDataObject>;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
        this.sortFunctionCollection();
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.functionPlayableCollectionLogger = Log.getInstance().getLogger('FunctionPlayableDataObjectCollection');
        this.functionPlayableCollectionLogger.debug('constructorPreprocessing(): Playable Function Collection');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Override base.  Creates playable versions of the Animate Function.
     *
     * @param element -> root level Element to construct Function from.
     * @returns {AnimatePlayableDataObject}
     */
    protected createAnimate(element: ElementDataObject): AnimatePlayableDataObject {
        return new AnimatePlayableDataObject(element);
    }

    /**
     * Override base.  Creates playable version of Change Page Function
     *
     * @param element -> root level Element to construct Function from.
     * @returns {ChangePagePlayableDataObject}
     */
    protected createChangePage(element: ElementDataObject): ChangePagePlayableDataObject {
        return new ChangePagePlayableDataObject(element);
    }

    /**
     * Override base.  Creates playable version of Delay Function
     *
     * @param element -> root level Element to construct Function from.
     * @returns {DelayPlayableDataObject}
     */
    protected createDelay(element: ElementDataObject): DelayPlayableDataObject {
        return new DelayPlayableDataObject(element);
    }

    /**
     * Overrides base.  Creates playable versions of DisplayMessage Function.
     *
     * @param element -> root level Element to construct Function from.
     * @returns {DisplayMessagePlayableDataObject}
     */
    protected createDisplayMessage(element: ElementDataObject): DisplayMessagePlayableDataObject {
        return new DisplayMessagePlayableDataObject(element);
    }

    /**
     * Overrides base.  Creates playable versions of SetProperty Function.
     *
     * @param element -> root level Element to construct Function from.
     * @returns {SetPropertyPlayableDataObject}
     */
    protected createSetProperty(element: ElementDataObject): SetPropertyPlayableDataObject {
        return new SetPropertyPlayableDataObject(element);
    }

    /**
     * Overrides base.  Creates playable versions of SetScorm Function.
     *
     * @param element -> root level Element to construct Function from.
     * @returns {SetScormPlayableDataObject}
     */
    protected createSetScorm(element: ElementDataObject): SetScormPlayableDataObject {
        return new SetScormPlayableDataObject(element);
    }

    /**
     * Overrides base.  Creates playable versions of SetVariable Function.
     *
     * @param element -> root level Element to construct Function from.
     * @returns {SetVariablePlayableDataObject}
     */
    protected createSetVariable(element: ElementDataObject): SetVariablePlayableDataObject {
        return new SetVariablePlayableDataObject(element);
    }

    /**
     * Overrides base.  Creates playable versions of Wait Function.
     *
     * @param element -> root level Element to construct Function from.
     * @returns {WaitPlayableDataObject}
     */
    protected createWait(element: ElementDataObject): WaitPlayableDataObject {
        return new WaitPlayableDataObject(element);
    }

    /**
     * Sorts the Function objects stored in functionCollection based on their order property.  Currently this only
     * needs to be done in the playable version of this class.
     */
    private sortFunctionCollection(): void {
        this.functionCollection.sort(
            (functionA: FunctionDataObject, functionB: FunctionDataObject) => {
                return functionA.order - functionB.order;
            }
        );
    }

    /**
     * Starts processing the Function Collection by running the individual Functions
     */
    public processFunctionCollection(): void {
        /*
        Reset functionsToRun list with functionCollection
        NOTE: slice is supposed to produce "shallow" copy which seems to indicate it won't work on complex objects
                but it seems to work ok here.  Keep an eye on this for problems.
        */
        this.functionsToRun = this.functionCollection.slice();

        console.log(this.functionCollection);
        console.log(this.functionsToRun);

        this.runFunction();
    }

    /**
     * Run individual Function from top of Collection
     */
    private runFunction(): void {
        if (this.functionsToRun.length > 0) {
            // Remove Function from top of functionsToRun array and run it
            ((this.functionsToRun.shift() as EventSystemDataObjectInterface) as FunctionPlayableDataObjectInterface)
                .runFunction();
        }
    }

    /**
     * To be called by child Function objects when their processing is complete.  In this case it runs the next Function
     * on the list if there are any left.  Otherwise it notifies parent that Function Collection processing is complete.
     */
    public childProcessComplete(): void {
        if (this.functionsToRun.length > 0) {
            this.runFunction();
        } else {
            (this._parent as FunctionGroupPlayableDataObject).childProcessComplete();
        }
    }
}



