import {PopUpDataObject} from '../data-objects/pop-up.data-object';
import {Subject} from 'rxjs/Subject';

/**
 * This Service Acts as a centralized place for opening popup
 */
export class PopUpService {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Comment me!
     */
    public popUpDataObject: PopUpDataObject;

    /**
     *Comment me!
     *
     * @type {Subject}
     */
    public displayPopUp: Subject<any> = new Subject<any>();

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * The constructor for this service.
     */
    constructor() {}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Once You Pop the Fun Don't Stop
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     *
     * @param popUpDataObject
     */
    public openPopUp( popUpDataObject: PopUpDataObject): void {
        this.popUpDataObject = popUpDataObject;
        this.displayPopUp.next();
    }

    /**
     * Convenience method for opening a generic system message.
     */
    public openSystemMessage( message: string ): void {
        const popUpData: PopUpDataObject = new PopUpDataObject(message);
        this.popUpDataObject = popUpData;

        this.displayPopUp.next();
    }
}




