import { DisplayMessageDataObject }
from '../../../../../shared/system-shared/model/data-objects/event-system/functions/display-message.data-object';
import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { FunctionPlayableDataObjectInterface } from './function.playable.data-object.interface';
import { FunctionPlayableDataObjectCollection } from '../function.playable.data-object.collection';

import { InjectorService } from '../../../../../shared/system-shared/model/services/injector.service';
import { PopUpService } from '../../../../../model/services/pop-up.service';
import {PopUpDataObject} from '../../../../../model/data-objects/pop-up.data-object';


/**
 * Playable version of DisplayMessage data object
 */
export class DisplayMessagePlayableDataObject extends DisplayMessageDataObject
    implements FunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableDisplayMessageLogger: Logger;

    private popupService: PopUpService = InjectorService.injector.get(PopUpService);

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableDisplayMessageLogger = Log.getInstance().getLogger('DisplayMessagePlayableDataObject');
        this.playableDisplayMessageLogger.debug('constructorPreprocessing(): Playable DisplayMessage');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * <p>Run DisplayMessage.</p>
     *
     */
    public runFunction(): void {
        const popUpData: PopUpDataObject = new PopUpDataObject(this.message);
        popUpData.titleText = this.messageTitle;
        popUpData.titleTextColor = this.messageTitleBarTextColor;
        popUpData.titleBarColor = this.messageTitleBarColor;
        popUpData.callBackFunction = () => (this._parent as FunctionPlayableDataObjectCollection).childProcessComplete();
        this.popupService.openPopUp(popUpData);
    }
}



