import { ElementBasedDataObject } from '../element-based.data-object';
import { ElementDataObject } from '../element.data-object';
import { FrameworkActionDataObject } from './framework-action.data-object';
import { FrameworkFunctionDataObject } from './framework-function.data-object';
import { Log, Logger } from '../../../log';


/**
 * Old Action System data object for holding FrameworkEvent related data
 */
export abstract class FrameworkEventDataObject extends ElementBasedDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * String used to identify Element associated with a FrameworkEvent
     * @type {string}
     */
    public static readonly FRAMEWORK_EVENT_ELEMENT_NAME: string = 'FrameworkEvent';

    /**
     * String used to identify Element associated with value
     * @type {string}
     */
    // protected static readonly VALUE_ELEMENT_NAME: string = 'Value';


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Reference to child FrameworkEvent
     * TODO: Can event have more than one function - does this need to be an array?
     */
    public frameworkFunction: FrameworkFunctionDataObject;

    /**
     * "Name" of event (i.e. functionButtonPressed)
     *
     * @internal: Not sure if this is actually used for anything.  Might be informational only.  Its value is stored in
     *              the value property of the localRoot Element object for FrameworkEvent which in itself is kind of odd
     *              (typically a property would be stored in its own separate child Element)
     */
    protected _eventName: string;

    /**
     * Reference to parent FrameworkAction
     */
    protected _parent: FrameworkActionDataObject;

    /**
     * Logger instance for logging
     */
    private frameworkEventLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.frameworkEventLogger = Log.getInstance().getLogger('FrameworkEventDataObject');

        super.constructorPreprocessing();
        this._eventName = this._localRoot.value;
        this.frameworkEventLogger.debug('constructorPreprocessing(): FrameworkEvent name = ' + this._eventName);

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }

    /**
     * Overrides base version.
     * Performs FrameworkEvent specific condition testing on the children of _localRoot to identify and setup
     * class specific properties
     *
     * @param  element -> Element to be parsed (typically by checking it's name property)
     */
    protected parseElement(element: ElementDataObject): void {
        this.frameworkEventLogger.debug('parseElement(): element.name = ' + element.name);
        switch (element.name) {
            case FrameworkFunctionDataObject.FRAMEWORK_FUNCTION_ELEMENT_NAME:
                // Create FrameworkFunction
                this.frameworkEventLogger.debug('parseElement(): FrameworkFunction');
                this.frameworkFunction = this.createFrameworkFunction(element);
                this.frameworkFunction.parent = this;
                break;
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    public get parent(): FrameworkActionDataObject {
        return this._parent;
    }

    public set parent(value: FrameworkActionDataObject) {
        this._parent = value;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    public get eventName(): string {
        return this._eventName;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Should be overridden in playable and editable versions of this class to return the appropriate version of
     * FrameworkFunctionDataObject
     *
     * @param element -> root level Element to construct FrameworkFunction from
     * @returns {FrameworkFunctionDataObject}
     */
    protected createFrameworkFunction(element: ElementDataObject): FrameworkFunctionDataObject {
        /*
         TODO: Since this class is abstract, this version of createEvent should never actually be called so will
         return undefined so that base classes can remain abstract
         */
        return undefined;
    }
}



