/**
 * A collection of LMS constant
 *
 */
export class LMSConstant {

    // these are the kinds of question types that the lms can accept
    public static TRUE_FALSE: string = 'true-false';
    public static MULTIPLE_CHOICE: string = 'choice';
    public static FILL_IN: string = 'fill-in';
    public static LONG_FILL_IN: string = 'long-fill-in';
    public static MATCHING: string = 'matching';
    public static PERFORMANCE: string = 'performance';
    public static SEQUENCING: string = 'sequencing';
    public static LIKERT: string = 'likert';
    public static NUMERIC: string = 'numeric';
    public static OTHER: string = 'other';

    // the lms accept these values for result
    public static CORRECT: string = 'correct';
    public static INCORRECT: string = 'incorrect';
    public static UNANTICIPATED: string = 'unanticipated';
    public static NEUTRAL: string = 'neutral';
    // it also accepts real numbers

    /**
     * The exit field in the scorm data model. cmi.exit
     */
    public static CMI_EXIT: string = 'cmi.exit';
    // cmi.exit possible values
    public static TIMEOUT: string = 'timeout';
    public static SUSPEND: string = 'suspend';
    public static LOGOUT: string = 'logout';
    public static NORMAL: string = 'normal';
    // it also accepts an empty charstring

    /**
     * the success status field in the scorm 2004 data model. cmi.succes_status
     */
    public static CMI_SUCCESS_STATUS: string = 'cmi.success_status';
    // lms values for success status (objective)
    public static PASSED: string = 'passed';
    public static FAILED: string = 'failed';
    public static UNKNOWN: string = 'unknown';

    /**
     * the success status field in the scorm 2004 data model. cmi.succes_status
     */
    public static CMI_COMPLETION_STATUS: string = 'cmi.completion_status';
    // lms values for completion status (objective)
    public static COMPLETED: string = 'completed';
    public static INCOMPLETE: string = 'incomplete';
    public static NOT_ATTEMPTED: string = 'not attempted';

}



