import { GetRandomNumberDataObject }
from '../../../../../shared/system-shared/model/data-objects/event-system/functions/get-random-number.data-object';
import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { ReturnValueFunctionPlayableDataObjectInterface } from './return-value-function.playable.data-object.interface';


/**
 * Playable version of GetRandomNumber data object
 */
export class GetRandomNumberPlayableDataObject extends GetRandomNumberDataObject
    implements ReturnValueFunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableGetRandomNumberLogger: Logger;

    /**
	 * <p>The return value of the playable FunctionDataObject implements ReturnValueFunctionPlayableDataObject Interface.
	 * It's public to allow its parent access to it when once its ready.
	 * There is no way of knowing what data type this will be. (Possibly String, Number, or Boolean).</p>
	 *
	 */
    public returnValue: any;



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableGetRandomNumberLogger = Log.getInstance().getLogger('GetRandomNumberPlayableDataObject');
        this.playableGetRandomNumberLogger.debug('constructorPreprocessing(): Playable GetRandomNumber');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * <p>Run GetRandomNumber.</p>
     *
     */
    public runFunction(): void {

        if (!isNaN (this.from) && !isNaN(this.to)) {
            // set min and max based of the given numbers
            let minimumValue: number = this.from;
            let maximumValue: number = this.to;

            if (this.from > this.to)	{
                minimumValue = this.to;
                maximumValue = this.from;
            }

            // generate the random number within the range, this range
            // is inclusive meaning both minimum and maximum are possible outcomes
            const range: number = maximumValue - minimumValue;

            let value: number = Math.random() * (range + 1);
            value = Math.floor(value + minimumValue);

            this.playableGetRandomNumberLogger.debug('returned random number = ' + value);
            // Set returnValue to property value
            this.returnValue = value;
        }
    }

}



