import { ElementBasedDataObject } from '../element-based.data-object';
import { ElementDataObject } from '../element.data-object';
import { FrameworkEventDataObject } from './framework-event.data-object';
import { Log, Logger } from '../../../log';


/**
 * Old Action System Data Object for holding FrameworkFunction related data
 *
 * @internal: Slightly unusual ElementDataObject structure.  Root is an ElementDataObject, but it has no properties /
 *              child ElementDataObjects, so might not really need to inherit from ElementBasedDataObject, but will do
 *              anyway to get built in constructor processing.  The root ElementDataObject value itself tells us what
 *              function it's supposed to run, so it's the only data we need to extract.  No override of the parent's
 *              parseElement method is necessary.
 */
export abstract class FrameworkFunctionDataObject extends ElementBasedDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * String used to identify an Element associated with a FrameworkFunction
     * @type {string}
     */
    public static readonly FRAMEWORK_FUNCTION_ELEMENT_NAME: string = 'FrameworkFunction';

    /**
     * Strings used to identify Action System Functions
     */
    protected static readonly GOTO_PAGE_FUNCTION: string = 'GotoPage';
    protected static readonly NEXT_PAGE_FUNCTION: string = 'NextPage';
    protected static readonly PREV_PAGE_FUNCTION: string = 'PrevPage';
    protected static readonly MESSAGE_FUNCTION: string = 'Message';
    protected static readonly VALIDATE_FUNCTION: string = 'Validate';
    // protected static readonly OPEN_COURSE_MAP_FUNCTION: string = 'OpenCourseMap'; // Is this even used?
    // protected static readonly CLOSE_TRAINING_FUNCTION: string = 'CloseTraining'; // Is this used?
    // protected static readonly PRINT_FUNCTION: string = 'Print'; // Is this used?
    // protected static readonly READ_MEMORY: string = 'RecallMemory'; // Used?
    // protected static readonly WRITE_MEMORY: string = 'SaveMemory'; // Used?
    // protected static readonly GET_VALUE: string = 'GetValue'; // Used?
    // protected static readonly SET_VALUE: string = 'SetValue'; // Used?


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * "Name" of FrameworkFunction (i.e. NextPage)
     *
     * @internal: Its value is stored in the value property of the localRoot Element object for FrameworkFunction which
     *              in itself is kind of odd (typically a property would be stored in its own separate child Element).
     */
    protected functionName: string;

    /**
     * Reference to parent FrameworkEvent
     */
    protected _parent: FrameworkEventDataObject;

    /**
     * Logger instance for logging
     */
    private frameworkFunctionLogger: Logger = Log.getInstance().getLogger('FrameworkFunctionDataObject');


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.frameworkFunctionLogger = Log.getInstance().getLogger('FrameworkFunctionDataObject');

        super.constructorPreprocessing();
        this.functionName = this._localRoot.value;
        this.frameworkFunctionLogger.debug('constructorPreprocessing(): FrameworkFunction name = ' + this.functionName);

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    public set parent(value: FrameworkEventDataObject) {
        this._parent = value;
    }

}



