import { ElementBasedDataObject } from '../element-based.data-object';
import { Log, Logger } from '../../../log';
import { ElementDataObject } from '../element.data-object';
import { FrameworkEventDataObject } from './framework-event.data-object';
import { ComponentDataObject } from '../components/component.data-object';


/**
 * Old Action System Data Object for holding FrameworkAction related data
 */
export abstract class FrameworkActionDataObject extends ElementBasedDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * String used to identify an Element associated with a FrameworkAction
     * @type {string}
     */
    public static readonly FRAMEWORK_ACTION_ELEMENT_NAME: string = 'Actions';


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Reference to child FrameworkEvent
     * TODO: Can action have more than one event - does this need to be an array?
     */
    protected _frameworkEvent: FrameworkEventDataObject;

    /**
     * Reference to parent ComponentDataObject of this action
     */
    protected _parent: ComponentDataObject;

    /**
     * Logger instance for logging
     */
    private frameworkActionLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.frameworkActionLogger = Log.getInstance().getLogger('FrameworkActionDataObject');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }

    /**
     * Overrides base version.
     * Performs FrameworkAction specific condition testing on the children of _localRoot to identify and setup
     * class specific properties
     *
     * @param  element -> Element to be parsed (typically by checking it's name property)
     */
    protected parseElement(element: ElementDataObject): void {
        switch (element.name) {
            case FrameworkEventDataObject.FRAMEWORK_EVENT_ELEMENT_NAME:
                this.frameworkActionLogger.debug('parseElement(): FrameworkEvent');
                // Create FrameworkEvent
                this._frameworkEvent = this.createFrameworkEvent(element);
                this._frameworkEvent.parent = this;
                break;
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    public get frameworkEvent(): FrameworkEventDataObject {
        return this._frameworkEvent;
    }

    ////////////////////////////////////////////////////////////////

    public get parent(): ComponentDataObject {
        return this._parent;
    }

    public set parent(value: ComponentDataObject) {
        this._parent = value;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Should be overridden in playable and editable versions of this class to return the appropriate version of
     * FrameworkEventDataObject
     *
     * @param element -> root level Element to construct FrameworkEvent from
     * @returns {FrameworkEventDataObject}
     */
    protected createFrameworkEvent(element: ElementDataObject): FrameworkEventDataObject {
        /*
         TODO: Since this class is abstract, this version of createEvent should never actually be called so will
         return undefined so that base classes can remain abstract
         */
        return undefined;
    }
}



