import { Observable }   from 'rxjs/Observable';

import { Transaction }  from '../../shared/system-shared/model/transactions/transaction';

import { LoadManifestTaskHandler }  from './transaction-task-handlers/load-Manifest.task-handler';


/**
 * This class is responsible for loading the manifest file's data from the various TransactionDataSources
 * (server/file/memory).
 *
 * @see Transaction
 * @see TransactionDataSource
 */
export class LoadManifestTransaction extends Transaction {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new LoadManifestTransaction instance.
     */
    constructor() {

        super('LoadManifest');
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * @inheritDoc
     */
    public startTransaction(): Observable<any> {

        // add TaskHandler for loading Manifest data
        const taskHandler: LoadManifestTaskHandler = new LoadManifestTaskHandler();
        taskHandler.buildRequestXml();
        this.addTaskHandler(taskHandler);

        // Send request to get processed
        return super.startTransaction();
    }
}



