import { Component, Input } from '@angular/core';
import { HtmlDataObject } from '../../shared/system-shared/model/data-objects/components/html.data-object';
// we need this to prevent error: unsafe value used in a resource URL context
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { AbstractComponent } from './abstract.component';

@Component({
    selector: 'player-html',
    templateUrl: 'html.component.html',
    styleUrls: ['../../app.component.css']
})

/**
 * Component for DIF Html component
 */
export class HtmlComponent extends AbstractComponent {

    /**
     * A reference to the Html Data object that provides the data for this component.
     */
    public html: HtmlDataObject;

    private safeUrl: SafeResourceUrl;

    constructor(private sanitizer: DomSanitizer) {
        super();
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.html = (this.dataSource as HtmlDataObject);
        this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(this.html.URL);
    }


    /**
     * Return ngStyle object for common properties
     */
    public get htmlTagStyleObject(): any {
        return this.commonStyleProperties;
    }


    /**
     * Sanitize the URL to prevent error: unsafe value used in a resource URL context
     */
    public get sanitizedURL(): any {
        return this.safeUrl
    }

}



