import { ReferenceDataObject } from '../data-objects/reference.data-object';

export class ReferenceService {

    /**
     * Public collection of course references
     */
    public references: Array<ReferenceDataObject>;

    constructor() {
    }

    public serializeClass(): string {
        let outString: string = 'References:\n';
        for (let i: number = 0; i < this.references.length; i++) {
            outString += 'Ref #' + (i + 1).toString() + ':\n';
            outString += this.references[i].toString();
        }

        return outString;
    }

}



