import { Component }     from '@angular/core';
import { PageService }          from '../../../shared/system-shared/model/services/page.service';
import { UnitService }          from '../../../shared/system-shared/model/services/unit.service';
import {UnitDataObject} from "../../../shared/system-shared/model/data-objects/unit.data-object";

@Component({
    selector: "player-pathed-page-title",
    templateUrl: 'pathed-page-title.component.html',
    styleUrls:  [ '../user-interface.module.css' ]
})


/**
 * TODO: Comment Me!!
 */
export class PathedPageTitleComponent {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor for the Control Bar Component. Used to inject pageService and unitService
     *
     * @param pageService
     * @param unitService
     */
    constructor(private pageService: PageService, private unitService: UnitService) {}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The name of the course
     *
     * @returns {string}
     */
    public get courseName(): string {
        if (!this.unitService.unit) {
            return '';
        }
        return this.unitService.unit.unitName;
    }

    /**
     * The Name of the current page
     *
     * @returns {string}
     */
    public get pageName(): string {
        return this.pageService.pageName;
    }

    /**
     *  The Entire Unit path to the current page.
     *
     * @returns {string}
     */
    public get pagePath(): string {
        if (!this.pageService.page || !this.unitService.unit) {
            return '';
        }
         const unitBreadcrumbList: Array<UnitDataObject> =  this.unitService.getCurrentPageUnitBreadcrumb();
         let breadcrumbString: string = '';

        for ( let i: number = 0; i < unitBreadcrumbList.length; i++ ) {
            breadcrumbString += unitBreadcrumbList[i].unitName;
            if ( i !== unitBreadcrumbList.length - 1 ) {
                breadcrumbString += ' - ';
            }
        }

        return breadcrumbString;
    }

}



