import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { FunctionGroupDataObject } from '../../../../../shared/system-shared/model/data-objects/event-system/function-groups/function-group.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { FunctionGroupPlayableDataObjectCollection } from '../function-group.playable.data-object.collection';
import { FunctionPlayableDataObjectCollection } from '../function.playable.data-object.collection';


/**
 * Playable version of Function Group data object
 */
export class FunctionGroupPlayableDataObject extends FunctionGroupDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private functionGroupPlayableLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.functionGroupPlayableLogger = Log.getInstance().getLogger('FunctionGroupPlayableDataObject');
        this.functionGroupPlayableLogger.debug('constructorPreprocessing(): Playable Function Group');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Overrides base.  Creates and returns a playable version of FunctionDataObjectCollection object
     *
     * @param element -> root level Element to construct Function from.
     * @returns {FunctionPlayableDataObjectCollection}
     */
    protected createFunctionCollection(element: ElementDataObject): FunctionPlayableDataObjectCollection {
        return new FunctionPlayableDataObjectCollection(element);
    }

    /**
     * Run all Function Groups in collection.
     */
    public processFunctionGroup(): void {
        this.functionGroupPlayableLogger.debug('processFunctionGroup()');

        (this.functionCollection as FunctionPlayableDataObjectCollection).processFunctionCollection();
    }

    public childProcessComplete(): void {
        (this._parent as FunctionGroupPlayableDataObjectCollection).childProcessComplete(this);
    }
}



