import { ConditionBranchDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/condition-branch.data-object';
import { ConditionLeafPlayableDataObject } from './condition-leaf.playable.data-object';
import { ConditionNodeDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/condition-node.data-object';
import { ConditionNodePlayableDataObjectInterface } from './condition-node.playable.data-object.interface';
import { ElementDataObject } from '../../../../shared/system-shared/model/data-objects/element.data-object';
import { EventSystemDataObjectInterface }
    from '../../../../shared/system-shared/model/data-objects/event-system/event-system.data-object.interface';
import { Log, Logger } from '../../../../shared/system-shared/log';


/**
 * Playable version of Condition Branch data object
 * @inheritDoc
 */
export class ConditionBranchPlayableDataObject extends ConditionBranchDataObject
    implements ConditionNodePlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private conditionBranchPlayableLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * @inheritDoc
     */
    protected constructorPreprocessing(): void {
        this.conditionBranchPlayableLogger = Log.getInstance().getLogger('ConditionBranchPlayableDataObject');
        this.conditionBranchPlayableLogger.debug('constructorPreprocessing(): Playable Condition Branch');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Overrides base.  Creates and returns playable version of Condition Leaf object.
     * @inheritDoc
     * @param sourceElement -> root level Element to construct Condition Leaf from.
     * @returns {ConditionLeafPlayableDataObject}
     */
    protected createConditionLeaf(sourceElement: ElementDataObject): ConditionLeafPlayableDataObject {
        return new ConditionLeafPlayableDataObject(sourceElement);
    }

    /**
     * Overrides base.  Creates and returns playable version of Condition Branch object.
     * @inheritDoc
     * @param sourceElement -> root level Element to construct Condition Branch from.
     * @returns {ConditionBranchPlayableDataObject}
     */
    protected createConditionBranch(sourceElement: ElementDataObject): ConditionBranchDataObject {
        return new ConditionBranchPlayableDataObject(sourceElement);
    }

    /**
     * Evaluates result of Condition Node data structure by recursively evaluating and comparing the child nodes.
     * @returns {boolean}
     */
    public evaluateCondition(): boolean {
        this.conditionBranchPlayableLogger.debug('evaluateCondition()');

        let result: boolean;

        switch (this.logicalOperator) {
            case ConditionBranchDataObject.AND_OPERATOR:
                // Returns true if result of all Condition Nodes is true
                result = false;
                result = this.conditionNodeCollection.every((conditionNode: ConditionNodeDataObject) => {
                    return (
                        ((conditionNode as EventSystemDataObjectInterface) as ConditionNodePlayableDataObjectInterface)
                            .evaluateCondition()
                    );
                });
                this.conditionBranchPlayableLogger.debug('evaluateCondition(): AND - result - ' + result);
                break;
            case ConditionBranchDataObject.OR_OPERATOR:
                // Returns true if the result of any Condition node is true
                result = false;
                result = this.conditionNodeCollection.some((conditionNode: ConditionNodeDataObject) => {
                    return (
                        ((conditionNode as EventSystemDataObjectInterface) as ConditionNodePlayableDataObjectInterface)
                            .evaluateCondition()
                    );
                });
                this.conditionBranchPlayableLogger.debug('evaluateCondition(): OR - result - ' + result);
                break;
        }

        return result;
    }
}



