import { XmlParser } from '../transactions/xml-parser';

////////////////////////////////////////////////////////////////
///
///		UnitType
///
///     container class for unit type data
///
////////////////////////////////////////////////////////////////

export class UnitTypeDataObject {

    //////////////////// Private Variables ////////////////////

    private _unitTypeID: number = 0;
    private _unitTypeName: string = '';
    private _unitTypeDesc: string = '';
    private _unitLevel: number = 0;


    constructor() {
    }


    ///////////////////////////////////////////////////////////
    ////////// Getters / Setters
    ///////////////////////////////////////////////////////////

    public get unitTypeDesc(): string {
        return this._unitTypeDesc;
    }

    public set unitTypeDesc(value: string) {
        this._unitTypeDesc = value;
    }

    ///

    public get unitTypeID(): number {
        return this._unitTypeID;
    }

    public set unitTypeID(value: number) {
        this._unitTypeID = value;
    }

    ///

    public get unitLevel(): number {
        return this._unitLevel;
    }

    public set unitLevel(value: number) {
        this._unitLevel = value;
    }

    ///

    public get unitTypeName(): string {
        return this._unitTypeName;
    }

    public set unitTypeName(value: string) {
        this._unitTypeName = value;
    }

    ////////////////////////////////////////////////////////////
    ////////// XML Handling
    ////////////////////////////////////////////////////////////

    /**
    * This function initializes the data of a unit type that is created in the reponse of a server transaction
    */
    public initializeDataFromServer(unitTypeXML: Element): void {
        this.unitLevel = +XmlParser.getAttribute(unitTypeXML, 'UnitLevel');
        this.unitTypeDesc = XmlParser.getAttribute(unitTypeXML, 'UnitTypeDesc');
        this.unitTypeID = +XmlParser.getAttribute(unitTypeXML, 'UnitTypeID');
        this.unitTypeName = XmlParser.getAttribute(unitTypeXML, 'UnitTypeName');
    }


    ////////////////////////////////////////////////////////////
    ////////// Debug Functions
    ////////////////////////////////////////////////////////////

    public toString(): string {
        let outString: string;
        outString = 'UnitTypeID = ' + this._unitTypeID + ';';
        outString += ' UnitLevel = ' + this._unitLevel + ';';
        outString += ' UnitTypeName = ' + this._unitTypeName + ';';
        outString += ' UnitTypeDesc = ' + this._unitTypeDesc;

        return outString;
    }
}



