import { Component } from '@angular/core';

import { AbstractComponent }
    from './abstract.component';
import { RadioButtonDataObject }
    from '../../shared/system-shared/model/data-objects/components/radio-button.data-object';


@Component({
    selector: 'player-radio-button',
    templateUrl: 'radio-button.component.html',
    styleUrls:  [ '../../app.component.css' ]
})


/**
 * The view model of the application's Angular radio button component.
 */
export class RadioButtonComponent extends AbstractComponent {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * A convenience reference to the dataSource that provides the model data for this component cast as an actual
     * RadioButtonDataObject.
     *
     * @default undefined
     */
    private radioButton: RadioButtonDataObject;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The style Object used by the ngStyle directive that updates the inline style attribute of the input tag used in
     * this component's template.
     *
     * @default A style Object containing the CSS properties for this component's input tag.
     */
    public get inputTagStyle(): Object {

        return this.commonStyleProperties;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Angular Lifecycle Hooks
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * @inheritDoc
     */
    public ngOnInit(): void {

        super.ngOnInit();

        this.radioButton = this.dataSource as RadioButtonDataObject;
    }
}



