import { InjectorService } from '../../shared/system-shared/model/services/injector.service';
import {PageService } from '../../shared/system-shared/model/services/page.service';
import { ScormService } from '../../model/services/scorm-service';
import { Interaction } from '../../model/services/scorm/interaction';
import { LMSConstant } from '../../model/services/scorm/lms.constant';
import {ComponentDataObject} from '../../shared/system-shared/model/data-objects/components/component.data-object';
import {PopUpService} from '../../model/services/pop-up.service';


/**
 * Base component for all quiz components
 */
export class QuizComponent {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    protected interaction: Interaction;

    protected interactionID: string;

    protected learnerResponse: string = '';
    protected learnerAttempt: number = 0;
    protected gotoNextPage: boolean = false;

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Common protected constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    protected quizBorderStyle: string = 'solid 1px black';
    protected borderRadius: number = 10;
    protected borderWidth: number = 2;
    protected borderColor: string = 'Black';
    protected backColor: string = 'white';
    protected fontName: string = 'Verdana'; // default font name
    protected fontColor: string = 'Black'; // default font color
    protected fontSize: number = 20;
    protected paddingRight: number = 5;
    protected paddingLeft: number = 5;
    // protected paddingTop: number = 5;
    // protected paddingBottom: number = 5;
    protected submitButtonWidth: number = 172;
    protected submitButtonHeoght: number = 35;
    protected submitButtonFontSize: number = 18;
    protected submitButtonFontBold: string = 'Bold';
    protected submitButtonFontColor: string = 'Black';
    protected radioSize: number = 25;
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    // protected _quizNameElement: ElementDataObject;
    // protected _questionElement: ElementDataObject;

    /**
     * A reference to the ScormService and PageService
     */
    protected scormService: ScormService = InjectorService.injector.get(ScormService);
    private pageService: PageService = InjectorService.injector.get(PageService);



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * This is an empty constructor.
     * 
     */
    constructor() {

    }

    /**
     * initQuizComponent
     *
     * @param  quizName
     *
     */
    public initQuizComponent(quizName: string): void {

        this.interaction = this.scormService.GetInteractionByID(this.pageService.page.pageID);

        // if interaction is not available, create a new one
        if (this.interaction === undefined) {

            this.interaction = new Interaction(this.pageService.page.pageID, this.getQuizType(), [quizName]);
        } else if (this.interaction.learnerResponse !== undefined) {

            this.learnerResponse = this.interaction.learnerResponse;
        }
    }

    /**
     * submitPressed function validate the answer first then set and save the interaction
     *
     *
     */
    public submitPressed(): void {

        // console.log('###QuizComponent.submitPressed this.getCorrectResponses()###' + this.getCorrectResponses());
        if (this.validateAnswer()) {

            this.interaction.addCorrectResponse(this.getCorrectResponses());

            // Added to save the actual text and not just the choice for Matching
            this.interaction.addCorrectResponseText(this.getCorrectResponsesText());

            this.interaction.setLearnerResponse(this.getLearnerResponse());
            this.interaction.setLearnerResponseText(this.getLearnerResponseText());
            this.interaction.description = this.getDescription();

            console.log('###QuizComponent.submitPressed() interaction = ' + this.interaction.serializeToString());

            this.scormService.saveInteraction(this.interaction);

            console.log('###QuizComponent.submitPressed loadNextPage###');
            if (this.gotoNextPage) {

                this.pageService.loadNextPage();
            }
        }
    }

    /** 
     * getCorrectResponses function need to be overriden in its derived class
     *
     * @returns string
     */
    protected getCorrectResponses(): string {
        return '';
    }

    /**
     * getCorrectResponsesText function need to be overriden in its derived class
     *
     * @returns string
     */
    protected getCorrectResponsesText(): string {
        return '';
    }

    /**
     * getLearnerResponse function need to be overriden in its derived class
     *
     * @returns string
     */
    protected getLearnerResponse(): string {
        return '';
    }

    /**
     * getLearnerResponseText function need to be overriden in its derived class
     *
     * @returns string
     */
    protected getLearnerResponseText(): string {
        return '';
    }

    /**
     * getDescription function need to be overriden in its derived class
     *
     * @returns string
     */
    protected getDescription(): string {
        return '';
    }

    /** 
     * getQuizType function need to be overriden in its derived class
     *
     * @returns string
     */
    protected getQuizType(): string {
        return LMSConstant.OTHER;
    }

    /** 
     * validateAnswer function need to be overriden in its derived class
     *
     * @returns boolean
     */
    protected validateAnswer(displayAlert: boolean = true): boolean {
        return true;
    }

    /** 
     * displayMessage function, will be replaced by DIF displayMessage
     *
     */
    protected displayMessage(message: string): void {
        InjectorService.injector.get(PopUpService).openSystemMessage(message);
    }

    /**
     * Create quiz outer box style
     * @param component - child quiz component
     * @returns any - (ngStyle object)
     */
    protected getQuizOuterBoxStyle(component: ComponentDataObject): any {
        return {
            'top.px': component.y,
            'left.px': component.x,
            'width.px': component.width,
            'height.px': component.height,
            'position': 'absolute',
            'z-index': component.z,
            'border': this.quizBorderStyle,
        };
    }

    /**
     * Create a quiz rounded box style used for "text boxes" (question, answer, etc.)
     * @param component - child quiz component
     * @param left - absolute x position of the box
     * @param top - absolute y position of the box
     * @param width - width of the box
     * @param height - height of the box
     * @returns any - ng style object
     */
    protected getQuizBoxStyle(component: ComponentDataObject,
                        left: number,
                        top: number,
                        width: number,
                        height: number,
                        background: string = this.backColor,
                        fontSize: number = this.fontSize,
                        whiteSpace: string = 'pre-wrap'): any {
        return {
            'left.px': left,
            'top.px': top,
            'width.px': width,
            'height.px': height,
            'z-index': component.z,
            'position': 'absolute',
            'border-radius': this.borderRadius + 'px',
            'border-style': 'solid',
            'border-width': this.borderWidth,
            'border-color': this.borderColor,
            'background-color': background,
            'white-space': whiteSpace, // handle CR NL
            'overflow-y': 'auto',
            'font-family': this.fontName,
            'vertical-align': 'top',
            'font-size': fontSize + 'pt',
            'display': 'inline-block',
            'padding-left': this.paddingLeft + 'px',
            'padding-right': this.paddingRight + 'px',
        };
    }

    /**
     * Create a label style object
     * @param component
     * @param left
     * @param top
     * @param width
     * @param fontSize
     * @param fontWeight
     * @returns any - ng style object
     */
    protected getQuizLabelStyle(component: ComponentDataObject,
                                left: number,
                                top: number,
                                width: number,
                                fontSize: number,
                                fontWeight: string = 'normal',
                                textAlign: string = 'center'): any {
        return {
            'left.px': left,
            'top.px': top,
            'width.px': width,
            'z-index': component.z,
            'position': 'absolute',
            'font-family': this.fontName,
            'font-size': fontSize + 'pt',
            'font-weight': fontWeight,
            'text-align': textAlign,
            'display': 'inline-block',
        };
    }

    /**
     * Create a quiz button style
     * @param component
     * @param top
     * @param left
     * @returns any - ng style object
     */
    protected getQuizButtonStyle(component: ComponentDataObject, top: number, left: number): any {
        return {
            'width.px': this.submitButtonWidth,
            'left.px': left,
            'top.px': top,
            'height.px': this.submitButtonHeoght,
            'position': 'absolute',
            'font-size': this.submitButtonFontSize + 'pt',
            'font-color': this.submitButtonFontColor,
            'fontWeight': this.submitButtonFontBold,
            'z-index': component.z,
            'font-family': this.fontName,
        };
    }

    /**
     * Create a quiz radio button style
     * @param component
     * @param top
     * @param left
     * @returns - ng style object
     */
    protected getRadioButtonStyle(component: ComponentDataObject, top: number, left: number): any {
        return {
            'width.px': this.radioSize,
            'height.px': this.radioSize,
            'left.px': left,
            'top.px': top,
            'position': 'absolute',
            'enabled': 'true',
            'z-index': component.z,
        };
    }

    /**
     * Create a quiz answer tile style (letters A, B,...)
     * @param component
     * @param top
     * @param left
     * @param width
     * @param height
     * @returns - ng style object
     */
    protected getQuizAnswerTitleStyle(component: ComponentDataObject,
                                      top: number,
                                      left: number,
                                      width: number,
                                      height: number,
                                      fontSize: number = this.fontSize): any {
        return {
            'width.px': width,
            'z-index': component.z,
            'position': 'absolute',
            'left.px': left,
            'top.px': top,
            'height.px': height,
            'font-size': this.fontSize + 'pt',
            'font-color': this.fontColor,
            'font-family': this.fontName,
            'display': 'flex',
            'align-items': 'center',
            'justify-content': 'center',
        };
    }
}



