import { ElementDataObject } 			from './element.data-object';
import { ComponentDataObject } 			from './components/component.data-object';
import { ComponentContainerDataObject }	from './components/component-container.data-object';
import { ContentWindow }				from '../content-window';
import { PagePropertiesDataObject }				from './page-properties.data-object';

/**
 * Desc: Class to hold Page object
 *
 * TODO: Fix this comment!!!
 *
 * TODO: Consider abstracting the page Data Retrieval one more level this class represents the page data as well as the
 * content (template Holder) it is currently both created from XML and Element Based. This is not clear!
 */
export class PageDataObject extends ComponentContainerDataObject{

	/**
     *
     */
    public type: string = 'Page';

	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

	private _pageXML: Element;

	/**
	 *	//I don't think i use this anymore
	 */
	private _pageData: ElementDataObject;

    /**
     * ??? not sure what this is for So lets just store it
     */
    private _contentWindow: ContentWindow;

    /**
     * PageID of this page object
     */
    private _pageID: string ;

    /**
     * PageName of this page object
     */
    private _pageName: string ;

    /**
     * Page Description of this page object
     */
    private _pageDesc: string ;


	/**
	 * is a content window treated different then components?
	 */
	//protected _components: Array<ComponentDataObject> = new Array<ComponentDataObject>();



    /**
     * probably just gonna be passed on so maybe i don't need to parse this here
     */
    private _children: Array<ElementDataObject>;

	/**
	 * A refrence to the Page Properties data Object witch contains the Information held in the PageAttributes Tag.
	 */
	public pageProperties: PagePropertiesDataObject;


	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	////////// Constructor / Initialization
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor
     *
     * @param sourceElement - ElementDataObject containing content (from Element table - Component/Template/Page)
     * @param pageXML - Page metadata (from Page table)
     */
    constructor(sourceElement: ElementDataObject, pageXML: Element ) {
		super(sourceElement);

		// this.parseElementChildren();

		this.parsePageAttributesFromPageTag(pageXML);
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    public get pageXML(): Element {
		return this._pageXML;
	}

	public set pageXML(value: Element) {
		this._pageXML = value;
	}

    public get pageID(): string  {
		return this._pageID;
	}

	public set pageID(value: string ) {
		this._pageID = value;
	}

	public get pageName(): string  {
		return this._pageName;
	}

	public set pageName(value: string ) {
		this._pageName = value;
	}

	public get pageDesc(): string  {
		return this._pageDesc;
	}

	public set pageDesc(value: string ) {
		this._pageDesc = value;
	}

	//////////////////////////////////////////////////////////

	/**
	 *
	 */
	public get pageData(): ElementDataObject {
		return this._pageData;
	}

	public set pageData(newPageData: ElementDataObject) {
		this._pageData = newPageData;
	}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Parsing methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Parse all page specific properties
     * Overrides parent method
     *
     * @param   child: ElementDataObject
     */
    protected parseElement(child: ElementDataObject): void {
        // if the child is not Name, Visible, or PageAttributes then lets handle this as something page specific
        switch (child.name) {
            case 'PageAttributes':
                this.pageProperties = new PagePropertiesDataObject( child );
                this.components.push(this.pageProperties);
                break;
            case 'ContentWindow':
                // TODO:Do we need to special case this like this? I saving it as a component here anyway?
                this._contentWindow = new ContentWindow(child);
                this.components.push(this._contentWindow);
                break;
            default:
                break;
        }

        // NOTE: We cannot call ComponentDataObject parseElement method from here directly (super.super)
        //       Therefore, we created parseProperties method on ComponentDataObject to substitute its parseElement method
        //       which has the same name in all component data objects
        // TODO: If it can be done in a more elegant name - just do it!
        this.parseProperties(child);
        super.parseElement(child); // parse components
    }

	/**
	 * Pulls out the attributes from the Page tag that define necessary properties of this page object.
	 *
	 * @param pageXML	The Page Tag from the Page data XML
	 */
	private parsePageAttributesFromPageTag(pageXML: Element ): void {
		this.pageXML = pageXML;
		this.pageID = pageXML.getAttribute('PageID');
		this.pageName = pageXML.getAttribute('PageName');
		this.pageDesc = pageXML.getAttribute('PageDesc');
	}

}



