import { CasePlayableDataObject } from './case.playable.data-object';
import { ElementDataObject } from '../../../../shared/system-shared/model/data-objects/element.data-object';
import { FunctionGroupDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/function-groups/function-group.data-object';
import { FunctionGroupDataObjectCollection }
    from '../../../../shared/system-shared/model/data-objects/event-system/function-group.data-object.collection';
import { FunctionGroupPlayableDataObject } from './function-groups/function-group.playable.data-object';
import { Log, Logger } from '../../../../shared/system-shared/log';


/**
 * Playable version of Function Group Collection
 */
export class FunctionGroupPlayableDataObjectCollection extends FunctionGroupDataObjectCollection {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Keeps track of which Function Groups in functionGroupCollection have been processed (since portions of the list
     * can be run both synchronously and asynchronously)
     */
    private processIndex: number;

    /**
     * Holds reference to the "Previous" Function Group that the "Run After Previous" Function Group is waiting for to
     * complete before running
     */
    private waitForPreviousFunctionGroup: FunctionGroupDataObject;

    /**
     * List of Function Groups whose processing has completed (could probably be a simple count, but this is a little
     * more robust if more details is needed)
     */
    private completedFunctionGroups: Array<FunctionGroupPlayableDataObject>;

    /**
     * Logger instance for logging
     */
    private functionGroupPlayableCollectionLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.functionGroupPlayableCollectionLogger =
            Log.getInstance().getLogger('FunctionGroupPlayableDataObjectCollection');
        this.functionGroupPlayableCollectionLogger.debug(
            'constructorPreprocessing(): Playable Function Group Collection');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Overrides base.  Creates and returns a Playable version of Function Group object
     *
     * @param sourceElement -> root level Element to construct Function Group from.
     * @returns {FunctionGroupPlayableDataObject}
     */
    protected createFunctionGroup(sourceElement: ElementDataObject): FunctionGroupPlayableDataObject {
        return new FunctionGroupPlayableDataObject(sourceElement);
    }

    /**
     * Run all Function Groups in collection.
     */
    public processFunctionGroupCollection(): void {
        this.functionGroupPlayableCollectionLogger.debug('processFunctionGroupCollection()');

        // Reset index and completedFunctionGroups to start from beginning
        this.processIndex = 0;
        this.completedFunctionGroups = [];

        // Start running the Function Groups
        this.runFunctionGroupUntilDoneOrAfter();
    }

    /**
     * Runs all Function Groups in functionGroupCollection asynchronously until either there aren't any left or a run
     * "After Previous" Function Group is encountered
     */
    private runFunctionGroupUntilDoneOrAfter(): void {
        this.functionGroupPlayableCollectionLogger.debug(
            'runFunctionGroupUntilDoneOrAfter(): starting index = ' + this.processIndex);

        // Start running Function Groups in collection until either there are none left or an execute after is reached
        do {
            const functionGroup: FunctionGroupDataObject = this.functionGroupCollection[this.processIndex];
            const nextFunctionGroup: FunctionGroupDataObject = this.functionGroupCollection[this.processIndex + 1];

            // If the next Function Group runs "after" this one, set waitForPreviousFunctionGroup to this one
            if (nextFunctionGroup && nextFunctionGroup.execute === FunctionGroupDataObject.EXECUTE_AFTER_VALUE) {
                this.waitForPreviousFunctionGroup = functionGroup;
            }

            this.functionGroupPlayableCollectionLogger.debug(
                'runFunctionGroupUntilDoneOrAfter(): @index = ' + this.processIndex);
            // Run the Function Group
            (this.functionGroupCollection[this.processIndex++] as FunctionGroupPlayableDataObject)
                .processFunctionGroup();
        } while (
            (this.processIndex < this.functionGroupCollection.length) &&
            (this.functionGroupCollection[this.processIndex].execute !== FunctionGroupDataObject.EXECUTE_AFTER_VALUE));
    }

    /**
     * To be called by child Playable Function Group objects when their processing is complete so that processing at the
     * Collection level can continue.  In this case, if processing of the next Function Group in the collection is
     * waiting for a particular one to complete, it checks the completedFunctionGroup to the one its waiting for
     * (waitForPreviousFunctionGroup).  If it matches, it can call runFunctionGroupUntilDoneOrAfter to continue
     * processing.  If all of the Function Groups in the collection have been processed the parent Playable Case can
     * be notified (by calling its childProcessComplete method).
     *
     * @param event -> the FunctionGroupPlayableDataObject that is reporting that its processing is complete
     */
    public childProcessComplete(completedFunctionGroup: FunctionGroupPlayableDataObject): void {
        this.functionGroupPlayableCollectionLogger.debug(
            'childProcessComplete(): functionGroup = ' + completedFunctionGroup.name);

        // Track that the Function Group was completed
        this.completedFunctionGroups.push(completedFunctionGroup);

        this.functionGroupPlayableCollectionLogger.debug(
            'childProcessComplete(): total function groups = ' + this.functionGroupCollection.length);
        this.functionGroupPlayableCollectionLogger.debug(
            'childProcessComplete(): total function groups complete = ' + this.completedFunctionGroups.length);

        if (completedFunctionGroup === this.waitForPreviousFunctionGroup) {
            this.functionGroupPlayableCollectionLogger.debug(
                'childProcessComplete(): After Previous Function Group complete');
            // If the Function Group completed is the one we're waiting for, start running Function Groups again
            this.runFunctionGroupUntilDoneOrAfter();
        } else if (this.completedFunctionGroups.length === this.functionGroupCollection.length) {
            // If all of the Function Groups have finished processing, notify parent
            this.functionGroupPlayableCollectionLogger.debug('childProcessComplete(): All Function Groups completed');
            (this._parent as CasePlayableDataObject).childProcessComplete();
        }
    }
}



