import { GetMathDataObject }
from '../../../../../shared/system-shared/model/data-objects/event-system/functions/get-math.data-object';
import { ElementDataObject }
from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { ReturnValueFunctionPlayableDataObjectInterface } from './return-value-function.playable.data-object.interface';
import { ValuePlayableDataObject } from '../value.playable.data-object';


/**
 * Playable version of GetMath data object
 */
export class GetMathPlayableDataObject extends GetMathDataObject
    implements ReturnValueFunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableGetMathLogger: Logger;

    /**
	 * <p>The return value of the playable FunctionDataObject implements ReturnValueFunctionPlayableDataObject Interface.
	 * It's public to allow its parent access to it when once its ready.
	 * There is no way of knowing what data type this will be. (Possibly String, Number, or Boolean).</p>
	 *
	 */
    public returnValue: any;



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableGetMathLogger = Log.getInstance().getLogger('GetMathPlayableDataObject');
        this.playableGetMathLogger.debug('constructorPreprocessing(): Playable GetMath');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


    /**
     * Overrides base version.
     * Performs playable GetMath object specific condition testing on the children of _localRoot to identify and setup
     * class specific properties
     *
     * @param  element -> Element to be tested (typically by checking it's name property)
     */
    protected parseElement(element: ElementDataObject): void {
        super.parseElement(element);
        switch (element.name) {
            case GetMathDataObject.VALUE_OBJECT_ELEMENT_NAME:
                const valueObject: ValuePlayableDataObject = new ValuePlayableDataObject(element);
                valueObject.parent = this;
                this._valueList.push(valueObject);
                this.playableGetMathLogger.debug('parseElement(): name = ' + element.name);
                break;

        }
    }


    /**
     * Run Math calculation process
     *
     */
    public runFunction(): void {

        // list of numerical values
        const numericalValueList: number[] = [];

        let result: number;

        this.sortOrder();

        // first we get all the values of the children ValueDataObject;
        for ( const value of this._valueList ) {
            numericalValueList.push(+(value as ValuePlayableDataObject).returnValue);
        }

        if (numericalValueList.length > 1) {
            switch (this.operator) {
                case GetMathDataObject.ADD:
                    result = this.add(numericalValueList);
                    break;
                case GetMathDataObject.SUBTRACT:
                    result = this.subtract(numericalValueList);
                    break;
                case GetMathDataObject.MULTIPLY:
                    result = this.multiply(numericalValueList);
                    break;
                case GetMathDataObject.DIVIDE:
                    result = this.divide(numericalValueList);
                    break;
            }
            this.playableGetMathLogger.debug('returned math calculation value = ' + result);
            this.returnValue = result;
        }
    }


  	/**
	 * Function for adding up 2 values in the given list
	 */

    private add(values: number[]): number {
        return values[0] + values[1];
    }

	/**
	 * Function for subtracting value1 from value2 in the given list
	 */
    private subtract(values: number[]): number {
        return values[0] - values[1];
    }

	/**
	 * Function for multiply 2 values in the given list
	 */
    private multiply(values: number[]): number {
        return values[0] * values[1];
    }

	/**
	 * Function for dividing up 2 values in the given list
	 */
    private divide(values: number[]): number {
        if (values[1] !== 0) {
            return values[0] / values[1];
        }
        return values[0];
    }

}



