import { ElementDataObject } from '../../../../shared/system-shared/model/data-objects/element.data-object';
import { FrameworkActionDataObject }
    from '../../../../shared/system-shared/model/data-objects/action-system/framework-action.data-object';
import { FrameworkEventPlayableDataObject } from './framework-event.playable.data-object';
import { Log, Logger } from '../../../../shared/system-shared/log';
import { FrameworkFunctionDataObject } from '../../../../shared/system-shared/model/data-objects/action-system/framework-function.data-object';
import { FrameworkFunctionPlayableDataObject } from './framework-function.playable.data-object';


/**
 * Playable version of FrameworkActionDataObject
 * @inheritDoc
 */
export class FrameworkActionPlayableDataObject extends FrameworkActionDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private frameworkActionPlayableLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.frameworkActionPlayableLogger = Log.getInstance().getLogger('FrameworkActionDataObject');
        this.frameworkActionPlayableLogger.debug('constructorPreprocessing(): Playable FrameworkAction');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    public get defaultFunction(): FrameworkFunctionPlayableDataObject {
        return this.frameworkEvent.frameworkFunction as FrameworkFunctionPlayableDataObject;
    }


    public get correctFunction(): FrameworkFunctionPlayableDataObject {
        // TODO: Get the right function
        return this.frameworkEvent.frameworkFunction as FrameworkFunctionPlayableDataObject;
    }

    public get inCorrectFunction(): FrameworkFunctionPlayableDataObject {
        // TODO: Get the right function
        return this.frameworkEvent.frameworkFunction as FrameworkFunctionPlayableDataObject;
    }



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Overrides base.  Creates and returns playable version of FrameworkEvent.
     *
     * @param element -> root level Element to construct FrameworkEvent from
     * @returns {FrameworkEventPlayableDataObject}
     */
    protected createFrameworkEvent(element: ElementDataObject): FrameworkEventPlayableDataObject {
        return new FrameworkEventPlayableDataObject(element);
    }

    /**
     * Starts Action System processing
     */
    public processFrameworkAction(value?: string): void {
        this.frameworkActionPlayableLogger.debug('processFrameworkAction()');
        /*
        console.log('7777777 value = ');
        console.log(value);
        console.log('7777777 parent correct value = ');
        console.log(this.parent.correctValue);
        */

        (this.frameworkEvent as FrameworkEventPlayableDataObject).processFrameworkEvent();

        /*
        if (value === undefined) {
            this.defaultFunction.processFrameworkFunction();
        } else if (value === this.parent.correctValue) {
            this.correctFunction.processFrameworkFunction();
        } else {
            this.inCorrectFunction.processFrameworkFunction();
        }
        */
    }
}



