import { Component }        from '@angular/core';
import { ReferenceService } from '../../../shared/system-shared/model/services/reference.service';
import {ReferenceDataObject} from '../../../shared/system-shared/model/data-objects/reference.data-object';

@Component({
    selector: 'player-references',
    templateUrl: 'references.component.html',
    styleUrls:  [ '../user-interface.module.css' ]
})

/**
 * The ReferenceComponent contains a list of References
 */
export class ReferencesComponent {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The constructor for the ReferencesComponent
     *
     * @param referenceService
     */
    constructor(private referenceService: ReferenceService) {}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * An array list of the references for this course.
     *
     * @returns {Array<ReferenceDataObject>}
     */
    public get referenceArray(): Array<ReferenceDataObject> {
        return this.referenceService.references;
    }
}



