import { Directive } from '@angular/core';

import { EventDataObject } from '../../../shared/system-shared/model/data-objects/event-system/event.data-object';
import { EventHandlerDirective } from './event-handler.directive';
import { Log, Logger } from '../../../shared/system-shared/log';
import { RadioButtonDataObject }
    from '../../../shared/system-shared/model/data-objects/components/radio-button.data-object';


@Directive({
  selector: '[playerRadioButtonEventHandler]'
})

export class RadioButtonEventHandlerDirective extends EventHandlerDirective {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    ngOnInit(): void {
        super.ngOnInit();
        // Always add on change listener
        this.createEventListener('change', EventDataObject.ON_CHANGE);
    }

    /**
     * Process event
     *
     * overrides base version
     * @param   eventType   DIF defined event type, for example, OnClick, OnDoubleClick...
     */
    protected processEvent(event: Event, eventType: string): void {
        super.processEvent(event, eventType);
        if (eventType === EventDataObject.ON_CHANGE) {
            this.updateCheckedInDataSource(event);
        } else {
            super.processEvent(event, eventType);
        }
    }

    /**
     * Overrides base.  Adds call to updateCheckedInDataSource.
     *
     * @inheritDoc
     */
    protected processAction(event: Event): void {
        // NOTE: if Action and Event could run this method twice
        // TODO: check to make sure this doesn't cause any bugs, otherwise will have to deal with it
        this.updateCheckedInDataSource(event);
        super.processAction(event);
    }

    /**
     * Updates checked property in RadioButtonDataObject to match state of component / display object
     * TODO: Jay says the intention of this is to allow Event System to trigger the change event on the component to
     *          trigger a further Event System ON_SELECTED and any Action System validation processes.  Not sure at this
     *          point whether this behavior is desirable (Flash Player does have this behavior for the radio button, but
     *          not for other components.
     */
    private updateCheckedInDataSource(event: Event): void {
        (this.dataSource as RadioButtonDataObject).selected = (event.currentTarget as HTMLInputElement).checked;
    }

}



