import { ColorDataType } from '../../../../../shared/system-shared/model/data-objects/data-types/color.data-type';
import { GetTypedConstantDataObject }
from '../../../../../shared/system-shared/model/data-objects/event-system/functions/get-typed-constant.data-object';
import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { ReturnValueFunctionPlayableDataObjectInterface } from './return-value-function.playable.data-object.interface';
import { VariableDataObject } from '../../../../../shared/system-shared/model/data-objects/variable.data-object';


/**
 * Playable version of GetTypedConstant data object
 */
export class GetTypedConstantPlayableDataObject extends GetTypedConstantDataObject
    implements ReturnValueFunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableGetTypedConstantLogger: Logger;

	/**
	 * <p>Value of unknown data type this function will return.</p>
	 */
    private _typedValue: any;


    /**
	 * <p>The return value of the playable FunctionDataObject implements ReturnValueFunctionPlayableDataObject Interface.
	 * It's public to allow its parent access to it when once its ready.
	 * There is no way of knowing what data type this will be. (Possibly String, Number, or Boolean).</p>
	 *
	 */
    public returnValue: any;



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableGetTypedConstantLogger = Log.getInstance().getLogger('GetTypedConstantPlayableDataObject');
        this.playableGetTypedConstantLogger.debug('constructorPreprocessing(): Playable GetTypedConstant');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * <p>Run GetTypedConstant.</p>
     *
     */
    public runFunction(): void {
		// only ever needs to run once so first we check if _typedValue is still null
        if (!this._typedValue) {
            if (this.constantValue !== '' && this.type !== '') {
                switch (this.type) {
                    case VariableDataObject.VARIABLE_TYPE_BOOLEAN:
                        this._typedValue = (this.constantValue.toLowerCase() === 'true');
                        break;
                    case VariableDataObject.VARIABLE_TYPE_NUMBER:
                        const numberValue: number = +this.constantValue;
                        if ( isNaN(numberValue) ) {
                            this.playableGetTypedConstantLogger.debug
                            ('ERROR: Variable Value convertion failed : Value '
                            + this.constantValue + ' is not a Number');
                        } else {
                            this._typedValue = numberValue;
                        }
                        break;
                    case VariableDataObject.VARIABLE_TYPE_STRING:
                        // TODO: Should consider adding handling for color data types in constants
                        // Setup RegExp to test if string is a hex color
                        const colorPattern: RegExp = /^0x[0-9,a-f]{6}$/i;

                        // Test pattern
                        if (colorPattern.exec(this.constantValue)) {
                            // If hex color convert to ColorDataType and set typedValue to webColor
                            const color: ColorDataType = new ColorDataType(this.constantValue);
                            this._typedValue = color.webColor;
                        } else {
                            // Otherwise handle as regular string
                            this._typedValue = this.constantValue;
                        }
                        break;
                    default:
                        this.playableGetTypedConstantLogger.debug
                        ('ERROR: Variable Value convertion failed : type '
                        + this.type + ' Not Supported by the front end');
                        break;
                }
            } else {
                    this.playableGetTypedConstantLogger.debug
                    ('runFunction(): Error Missing required Data: Value = '
                    + this.constantValue + ' and Type = ' + this.type);
            }
        }
        this.playableGetTypedConstantLogger.debug('returned constant = ' + this._typedValue);
        // Set returnValue to value of constant
        this.returnValue = this._typedValue;
    }

}



