import {Component} from '@angular/core';
import {PopUpService} from '../../model/services/pop-up.service';
import {PopUpDataObject} from '../../model/data-objects/pop-up.data-object';
import {MdDialogRef} from '@angular/material';


@Component({
    selector: 'player-pop-up',
    templateUrl: 'pop-up.component.html',
    styleUrls:  [ 'user-interface.module.css' ],
})

/**
 * Comment This class
 */
export class PopUpComponent {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     *
     */
    popUpDataObject: PopUpDataObject;

    hasCancel: boolean = false;

    buttonWidth: number;

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     *
     * @param popUpService
     */
    constructor( private popUpService: PopUpService, private dialogRef: MdDialogRef<PopUpComponent>) {
        this.popUpDataObject = this.popUpService.popUpDataObject;

        if ( this.popUpDataObject.buttonConfiguration === 'ok') {
            this.hasCancel = false;
            this.buttonWidth = 108;
        } else if (this.popUpDataObject.buttonConfiguration === 'okCancel') {
            this.hasCancel = true;
            this.buttonWidth = 221;
        }

    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Event Handlers
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     *
     */
    public onOkClicked(): void {
        if (this.popUpDataObject.callBackFunction) {
            this.popUpDataObject.callBackFunction();
        }
        this.dialogRef.close();
    }

    public onCancelClicked(): void {
        // close this popup
        this.dialogRef.close();
    }

}



