import { UnitTypeDataObject } from '../data-objects/unit-type.data-object';

export class UnitTypeService {

    /**
     * Array of unit type data objects
     */
    public unitTypes: Array<UnitTypeDataObject>;

    constructor() {

    }


    /**
     * Return a string representation of available unit types
     */
    public serializeClass(): string {
        let outString: string = 'Unit types:\n';

        for (let i: number = 0; i < this.unitTypes.length; i++) {
            outString += this.unitTypes[i].toString() + '\n';
        }

        return outString;
    }

}



