import { ElementDataObject }    from '../element.data-object';

import { AudioDataObject }              from './audio.data-object';
import { ButtonDataObject }             from './button.data-object';
import { CheckboxDataObject }           from './checkbox.data-object';
import { ComponentDataObject }          from './component.data-object';
import { DropdownListDataObject }       from './dropdown-list.data-object';
import { HtmlDataObject }               from './html.data-object';
import { ImageDataObject }              from './image.data-object';
import { VideoDataObject }              from './video.data-object';
import { QuizMultipleChoiceDataObject } from './quiz-multiple-choice.data-object';
import { QuizFillInBlankDataObject }    from './quiz-fill-in-blank.data-object';
import { QuizMatchDataObject }          from './quiz-match.data-object';
import { QuizResultDataObject }         from './quiz-result.data-object';
import { RadioButtonDataObject }        from './radio-button.data-object';
import { TextDisplayDataObject }        from './text-display.data-object';
import { TextInputDataObject }          from './text-input.data-object';
import { TextInputAreaDataObject }      from './text-input-area.data-object';

import { Log, Logger }  from '../../../log';


/**
 * This is the base class for any data objects that drive view components which are designed to contain other view
 * components (e.g. ContentWindowComponent, PageComponent, etc.).
 */
export abstract class ComponentContainerDataObject extends ComponentDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * TODO: Rename this.
     */
    private _components: Array<ComponentDataObject>;

    /**
     * An instance of the logger class for outputting log messages.
     *
     * A string is passed to the logger factory to be used as part of the name of the logger.  This name appears before
     * each log statement that is output.
     *
     * @default undefined
     */
    private componentContainerDataObjectLogger: Logger = Log.getInstance().getLogger('ComponentContainerDataObject');


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Abstract Constructor - provides implementation common to all component container data objects.
     *
     * @param   sourceElement   The root of the ElementDataObject hierarchy which contains the source data we will be
     *                          constructed from.
     */
    constructor(sourceElement: ElementDataObject) {

        super(sourceElement);
    }

    /**
     * TODO: Comment me
     *
     * @param child
     */
    protected parseElement(child: ElementDataObject): void {

        this.componentContainerDataObjectLogger = Log.getInstance().getLogger('ComponentContainerDataObject');

        switch (child.name) {

            case 'Audio':
                const audio: AudioDataObject = new AudioDataObject(child);
                this.components.push(audio);
                this.componentContainerDataObjectLogger.debug(audio.serializeToString());
                break;

            case 'CheckBox':
                const checkBox: CheckboxDataObject = new CheckboxDataObject(child);
                this.components.push(checkBox);
                this.componentContainerDataObjectLogger.debug(checkBox.serializeToString());
                break;

            case 'DropDown':
                const dropDown: DropdownListDataObject = new DropdownListDataObject(child);
                this.components.push(new DropdownListDataObject(child));
                this.componentContainerDataObjectLogger.debug(dropDown.serializeToString());
                break;

            case 'FunctionButton':
                const button: ButtonDataObject = new ButtonDataObject(child);
                this.components.push(button);
                this.componentContainerDataObjectLogger.debug(button.serializeToString());
                break;

            case 'Html':
                const html: HtmlDataObject = new HtmlDataObject(child);
                this.components.push(html);
                this.componentContainerDataObjectLogger.debug(html.serializeToString());
                break;

            case 'Image':
                const image: ImageDataObject = new ImageDataObject(child);
                this.components.push(image);
                this.componentContainerDataObjectLogger.debug(image.serializeToString());
                break;

            case 'Movie':
                const video: VideoDataObject = new VideoDataObject(child);
                this.components.push(video);
                this.componentContainerDataObjectLogger.debug(video.serializeToString());
                break;

            case 'QuizWindowMC':
                const quizMC: QuizMultipleChoiceDataObject = new QuizMultipleChoiceDataObject(child);
                this.components.push(quizMC);
                this.componentContainerDataObjectLogger.debug(quizMC.serializeToString());
                break;

            case 'QuizWindowBlank':
                const quizBlank: QuizFillInBlankDataObject = new QuizFillInBlankDataObject(child);
                this.components.push(quizBlank);
                this.componentContainerDataObjectLogger.debug(quizBlank.serializeToString());
                break;

            case 'QuizWindowMatch':
                const quizMatch: QuizMatchDataObject = new QuizMatchDataObject(child);
                this.components.push(quizMatch);
                this.componentContainerDataObjectLogger.debug(quizMatch.serializeToString());
                break;

            case 'RadioButton':
                const radioButton: RadioButtonDataObject = new RadioButtonDataObject(child);
                this.components.push(radioButton);
                radioButton.parent = this;
                this.componentContainerDataObjectLogger.debug(radioButton.serializeToString());
                break;

            case 'ResultsWindow':
                const quizResult: QuizResultDataObject = new QuizResultDataObject(child);
                this.components.push(quizResult);
                this.componentContainerDataObjectLogger.debug(quizResult.serializeToString());
                break;

            case 'TextBox':
                this.addNewTextDataObject(child);
                break;

            case 'TextWindow':
                this.addNewTextDataObject(child);
                break;
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * TODO: Comment me
     */
    public get components(): Array<ComponentDataObject> {

        if (!this._components) { this._components = []; }
        return this._components;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Component Management
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     *
     * @param   sourceElement  The root of the ElementDataObject hierarchy which contains the source data to parse.
     */
    protected addNewTextDataObject(sourceElement: ElementDataObject): void {

        let isEditable: boolean = false;
        let isMultiline: boolean = false;

        for (const element of sourceElement.children) {

            switch (element.name) {

                case 'Editable':
                    if (element.value.toLowerCase() === 'true') {
                        isEditable = true;
                    }
                    break;

                case 'Multiline':
                    if (element.value.toLowerCase() === 'true') {
                        isMultiline = true;
                    }
                    break;
            }
        }

        if (isEditable) {
            if ( isMultiline ) {
                this.components.push(new TextInputAreaDataObject(sourceElement));
            } else {
                this.components.push(new TextInputDataObject(sourceElement));
            }
        } else {
            this.components.push(new TextDisplayDataObject(sourceElement));
        }
    }

    /**
     * TODO: Rename this to findDescendantComponent.
     *
     * Recursively search the descendants of this ComponentContainerDataObject to find the ComponentDataObject that has
     * the given element ID.
     *
     * NOTE:
     *          When we check a descendant that is also a ComponentContainerDataObject we must also check its
     *          descendants and so on.
     *
     * @param   componentElementID  The elementID of the ComponentDataObject we are trying to find.
     *
     * @returns The ComponentDataObject with the given elementID, otherwise undefined.
     */
    public findChildComponent(componentElementID: number): ComponentDataObject {

        let returnComponent: ComponentDataObject;

        for (const component of this.components) {
            if (component.id === componentElementID) {
                return component;
            } else if ( component instanceof ComponentContainerDataObject ) {
                returnComponent = (component as ComponentContainerDataObject).findChildComponent(componentElementID);
                if (returnComponent) {
                    return returnComponent;
                }
            }
        }
        return returnComponent;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Helper functions
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Concatenate class properties into a string
     */
    public serializeToString(): string {
        let outString: string = 'Component Container Data Object:\n';

        outString += 'height = ' + this.height + '\n';
        outString += 'id = ' + this.id + '\n';
        outString += 'name = ' + this.name + '\n';
        outString += 'visible = ' + this.visible + '\n';
        outString += 'width = ' + this.width + '\n';
        outString += 'x = ' + this.x + '\n';
        outString += 'y = ' + this.y + '\n';
        outString += 'z = ' + this.z + '\n';
        outString += 'localRoot = ' + this.localRoot.name;

        return outString;
    }
}



