import { Observable } from 'rxjs/Observable';
import { LoadPlayerInitDataTransaction } from '../transactions/load-player-init-data.transaction';


/*
 *  Service class to provide player related tasks, like load player init data.
 **/

export class PlayerService {


    constructor() {
    }

    /*
    * Load player init data - unit,type,reference,variable
    *
    *
    **/
    public loadInitData(): Observable<any> {
        const loadPlayerInitTransaction: LoadPlayerInitDataTransaction = new LoadPlayerInitDataTransaction();
        return loadPlayerInitTransaction.startTransaction();
    }

}



