import { InjectorService }  from '../../services/injector.service';
import { VariableService }  from '../../services/variable.service';

import { VariablePlayableDataObject }   from '../../../../../model/data-objects/variable.playable.data-object';

import { TransactionDataSource }    from '../transaction';
import { TransactionTaskHandler }   from './transaction-task-handler';

import { Log, Logger }  from '../../../log';
import { XmlParser }    from '../xml-parser';


/**
 * This task handler makes the request to load all of the course's user-defined variables.  The response is then parsed
 * and from that data an array of variable playable data objects is created in the variable service for use by the rest
 * of the application.
 *
 * @see TransactionTaskHandler
 * @see VariableService
 * @see VariablePlayableDataObject
 */
export class LoadUserVariablesTaskHandler extends TransactionTaskHandler {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * An instance of the logger class for outputting log messages.
     *
     * A string is passed to the logger factory to be used as part of the name of the logger.  This name appears before
     * each log statement that is output.
     *
     * @default undefined
     */
    private loadUserVariablesTaskHandlerLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new LoadUserVariablesTaskHandler instance.
     */
    constructor() {

        super('LoadVariableList');

        // If player is offline, data source is File, set FileURL
        if (this.dataSource === TransactionDataSource.File) {
            this.fileUrl = this.startupConfigurationService.variableFileUrl;
        }
    }

    /**
     * @inheritDoc
     */
    protected constructorPreprocessing(): void {

        this.loadUserVariablesTaskHandlerLogger = Log.getInstance().getLogger('LoadUserVariablesTaskHandler');

        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Request Handling
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Builds the XML for the load user-defined variables request.
     *
     * @param   unitID  The unit ID of the course whose user defined variables we wish to load.
     * @param   pageID  The page ID of the current page in the course whose user defined variables we wish to load.
     *
     * @returns The root element of the load user-defined variables request XML.
     */
    public buildRequestXml(unitID?: string, pageID?: string): Element {

        if (pageID) {
            this.requestXml.appendChild
            (
                XmlParser.xmlStringToElement(`<Page><PageID>${pageID}</PageID></Page>`)
            );

        } else {

            this.requestXml.appendChild
                (
                    XmlParser.xmlStringToElement(`<Unit><UnitID>${unitID}</UnitID></Unit>`)
                );
        }

        return this.requestXml;
    }

    /**
     * Creates the user-defined variable list inside the VariableService.
     *
     * @inheritDoc
     */
    public processResponse(): void {

        let responseXML: Element = this.responseData;
        if (this.dataSource === TransactionDataSource.File) {
            responseXML = XmlParser.findFirstDescendantByName(this.responseData, 'Response');
        }

        const variableService: VariableService = InjectorService.injector.get(VariableService);
        variableService.variables = [];

        const responseElements: Array<Element> = XmlParser.getChildElements(responseXML);

        for (const element of responseElements) {

            if (element.tagName === 'Variable') {

                const newVariable: VariablePlayableDataObject = new VariablePlayableDataObject();
                newVariable.initializeDataFromServer(element);
                variableService.variables.push(newVariable);
            }
        }

        this.loadUserVariablesTaskHandlerLogger.trace(variableService.serializeClass());
    }
}



