import { Component, Input } from '@angular/core';
import { QuizFillInBlankDataObject } from '../../shared/system-shared/model/data-objects/components/quiz-fill-in-blank.data-object';
import { QuizComponent } from './quiz.component';
import { LMSConstant } from '../../model/services/scorm/lms.constant';

@Component({
    selector:  'player-quiz-fill-in-blank',
    templateUrl: 'quiz-fill-in-blank.component.html'
})

/**
 * Component for DIF quiz Fill-In-Blank
 */
export class QuizFillInBlankComponent extends QuizComponent {
    // Question text box
    private questionTop: number = 29;
    private questionLeft: number = 27;
    private questionRight: number = 27;
    private questionHeight: number = 107;
    private questionPaddingLeft: number = 5;
    private questionPaddingRight: number = 5;
    // Answer title box
    private titleBoxLeft: number = 27;
    private titleBoxRight: number = 27;
    private titleBoxTop: number = 160 - 10;
    private titleBoxHeight: number = 40 + 70;
    private titleBoxBackground: string = '#D8D8D8';
    // Answer Title
    private titleFontColor: string = 'Black';
    private titleFontBold: string = 'Bold';
    private titleFontSize: number = 18;
    private titleHeight: number = 30;
    private titleLeft: number = 43;
    private titleTop: number = 160;
    // Answer
    private answerLeft: number = 43;
    private answerRight: number = 43;
    private answerInitTop: number = 198;
    private answerHeight: number = 40;
    private answerFontSize: number = 32;

    /**
     * The data object that contains the quizFillInBlank configuration information
     */
    @Input()   quizFillInBlank: QuizFillInBlankDataObject;

    /**
     * ngOnInit() call initQuizComponent to initialize QUIZ/SCORM by passing quizFillInBlank
     * 
     */
    ngOnInit(): void {

        this.initQuizComponent(this.quizFillInBlank.quizName);
    }

    /**
     * Creates a ngStyle object for quizFillInBlank frame
     * @returns any (ngStyle object)
     */
    public get quizTagStyleObject(): any {
        return super.getQuizOuterBoxStyle(this.quizFillInBlank);
    }

    /**
     * Creates a ngStyle object for quiz fill in blank question
     * @returns any (ngStyle object)
     */
    public get quizQuestionStyleObject(): any {
        const left: number = this.quizFillInBlank.x + this.questionLeft;
        const top: number = this.quizFillInBlank.y + this.questionTop;
        const width: number = this.quizFillInBlank.width
                            - this.questionLeft - this.questionRight
                            - this.questionPaddingLeft - this.questionPaddingRight;

        return this.getQuizBoxStyle(this.quizFillInBlank, left, top, width, this.questionHeight);

    }

    /**
     * Creates a ngStyle object for quiz fill in blank title box
     * @returns any (ngStyle object)
     */
    public get quizTitleBoxStyleObject(): any {
        const top: number = this.quizFillInBlank.y + this.titleBoxTop;
        const left: number = this.quizFillInBlank.x + this.titleBoxLeft;
        const width: number = this.quizFillInBlank.width - this.titleBoxLeft
            - this.titleBoxRight - this.paddingRight - this.paddingLeft;

        return this.getQuizBoxStyle(this.quizFillInBlank, left, top, width, this.titleBoxHeight, this.titleBoxBackground);

    }

    /**
     * Creates a ngStyle object for quiz fill in blank title
     * @returns any (ngStyle object)
     */
    public get quizTitleStyleObject(): any {
        const top: number = this.quizFillInBlank.y + this.titleTop;
        const left: number = this.quizFillInBlank.x + this.titleLeft;
        const width: number = this.quizFillInBlank.width - this.titleBoxLeft - this.titleBoxRight;

        return this.getQuizLabelStyle(this.quizFillInBlank,
            left, top, width, this.titleFontSize, this.titleFontBold, 'left');

    }

    /**
     * Creates a ngStyle object for quiz fill in blank answer
     * @returns any (ngStyle object)
     */
    public get quizAnswerStyleObject(): any {
        const top: number = this.quizFillInBlank.y + this.answerInitTop;
        const left: number = this.answerLeft + this.quizFillInBlank.x;
        const width: number = this.quizFillInBlank.width
            - this.answerLeft
            - this.paddingRight
            - this.paddingLeft
            - this.answerRight;

        return this.getQuizBoxStyle(this.quizFillInBlank,
            left, top, width, this.answerHeight,
            this.backColor, this.answerFontSize);

    }

    /**
     * Creates a ngStyle object for quizFillInBlank submit buttom
     * @returns any (ngStyle object)
     */
    public get quizButtonStyleObject(): any {
        const left: number = this.quizFillInBlank.x + (this.quizFillInBlank.width - this.submitButtonWidth) / 2;
        const top: number = this.quizFillInBlank.y + this.quizFillInBlank.height + 17;

        return this.getQuizButtonStyle(this.quizFillInBlank, top, left);

    }




    /**
     * getQuizType
     *
     */
    protected getQuizType(): string {

        return LMSConstant.FILL_IN;
    }

    /**
     * getLearnerResponse function
     *
     * @returns string
     */
    protected getLearnerResponse(): string {
        return this.learnerResponse;
    }


    /** 
     * getCorrectResponses function get correction response in array
     *
     * @returns string
     */
    protected getCorrectResponses(): string {

        return this.quizFillInBlank.correctAnswer;
    }


    /**
     * getDescription question/description
     *
     * @returns string
     */
    protected getDescription(): string {
        return this.quizFillInBlank.question;
    }


    /** 
     * validateAnswer function validate learner's response
     *
     * @returns boolean true if
     *          1) learner input is correct or
     *          2) learner input is incorrect AND multipleAttemptFlag false
     *          3) learner input is incorrect AND learnerAttempt is no less than allowedoption
     *
     */
    protected validateAnswer(displayAlert: boolean = true): boolean {
        let validate: boolean = true;

        if (this.getLearnerResponse().trim() === '') {

            validate = false;
            this.displayMessage('You must enter an answer to continue.');

        } else {

            // Leaner response is correct
            if (this.quizFillInBlank.correctAnswer === this.getLearnerResponse()) {

                // if feedbackFlag is true and feedBackCorrectMessage is not empty
                if (this.quizFillInBlank.feedBackFlag && this.quizFillInBlank.feedbackCorrectMessage !== '') {

                    this.displayMessage(this.quizFillInBlank.feedbackCorrectMessage);
                }
                this.gotoNextPage = true;

            } else { // Leaner response is incorrect

                this.learnerAttempt = this.learnerAttempt + 1;

                // if multipleAttemptFlag is true and learnerAttempt is less than allowed
                if (this.quizFillInBlank.multipleAttemptFlag
                    && this.quizFillInBlank.numberOfAttempts > this.learnerAttempt) {

                    this.displayMessage(this.quizFillInBlank.feedbackIncorrectAttempt);

                } else {

                    // if feedbackFlag is true and feedbackIncorrectMessage is not empty
                    if (this.quizFillInBlank.feedBackFlag && this.quizFillInBlank.feedbackIncorrectMessage !== '') {

                        this.displayMessage(this.quizFillInBlank.feedbackIncorrectMessage);
                    }
                    this.gotoNextPage = true;
                }
            }

        }

        console.log('---validateAnswer validate=' + validate);

        return validate;
    }

}




