import { ElementDataObject }    from '../element.data-object';
import { TextDataObject }       from './text.data-object';

import { Log, Logger }  from '../../../log';


/**
 * TODO:    Write description.
 * TODO:    Remove unused headers.
 */
export class TextInputAreaDataObject extends TextDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * An instance of the logger class for outputting log messages.
     *
     * A string is passed to the logger factory to be used as part of the name of the logger.  This name appears before
     * each log statement that is output.
     *
     * @default undefined
     */
    private textInputAreaDataObjectLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new TextInputAreaDataObject instance.
     *
     * @param   sourceElement   The root of the ElementDataObject hierarchy which contains the source data we will be
     *                          constructed from.
     */
    public constructor(sourceElement: ElementDataObject) {

        super(sourceElement);
    }

    /**
     * @inheritDoc
     */
    protected constructorPreprocessing(): void {

        this.textInputAreaDataObjectLogger = Log.getInstance().getLogger('TextInputAreaDataObject');

        super.constructorPreprocessing();
    }
}



