import { Directive } from '@angular/core';
import { Log, Logger } from '../../../shared/system-shared/log';
import { EventDataObject } from '../../../shared/system-shared/model/data-objects/event-system/event.data-object';
import { EventHandlerDirective } from './event-handler.directive';
import { AudioDataObject } from '../../../shared/system-shared/model/data-objects/components/audio.data-object';


@Directive({
  selector: '[playerAudioEventHandler]'
})
export class AudioEventHandlerDirective extends EventHandlerDirective {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Lifecycle Hooks
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


    ngOnInit(): void {
        super.ngOnInit();
        // Add audio play/pause/stop related event emitter subscription
        this.addSubscription();
        // Always add on complete listener
        this.createEventListener('ended', EventDataObject.ON_COMPLETE);
    }

    /**
     * Parse event triggers
     *
     * overrides base version
     * @param   eventType   DIF defined event type, for example, OnClick, OnDoubleClick...
     */
    protected parseEventTriggers(eventType: string): void {
        super.parseEventTriggers(eventType);
        switch (eventType) {
            case EventDataObject.ON_PLAY:
                this.createEventListener('play', eventType);
                break;
            case EventDataObject.ON_PAUSE:
                this.createEventListener('pause', eventType);
                break;
        }
    }


   /**
     * Process event
     * overrides base version.
     *
     * @param   event       TODO: Add param description.
     * @param   eventType   An application defined event type (e.g. OnClick, OnDoubleClick, etc.).
     */
    protected processEvent(event: Event, eventType: string): void {
        super.processEvent(event, eventType);

        // Emit Audio's onComplete event
        const audioDataObject: AudioDataObject = this.dataSource as AudioDataObject;
        audioDataObject.onComplete.emit();
    }


    /**
    * Add audio play/pause/stop related event emitter subscription
    *
    */
    protected addSubscription(): void {
        const audioDataObject: AudioDataObject = this.dataSource as AudioDataObject;
        const audioHtmlObject: HTMLAudioElement = this.elementRef.nativeElement as HTMLAudioElement;
        audioDataObject.onPlay.subscribe( () => {
            audioHtmlObject.play();
        } );
        audioDataObject.onPause.subscribe( () => {
            audioHtmlObject.pause();
        } );
        audioDataObject.onStop.subscribe( () => {
            audioHtmlObject.pause();
            audioHtmlObject.currentTime = 0;
        } );
    }

}



