import { GetVariableDataObject }
from '../../../../../shared/system-shared/model/data-objects/event-system/functions/get-variable.data-object';
import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { InjectorService } from '../../../../../shared/system-shared/model/services/injector.service';
import { VariableService } from '../../../../../shared/system-shared/model/services/variable.service';
import { ReturnValueFunctionPlayableDataObjectInterface } from './return-value-function.playable.data-object.interface';



/**
 * Playable version of GetVariable data object
 */
export class GetVariablePlayableDataObject extends GetVariableDataObject
    implements ReturnValueFunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableGetVariableLogger: Logger;

    private variableService: VariableService = InjectorService.injector.get(VariableService);

    /**
     * The return value of the playable FunctionDataObject implements ReturnValueFunctionPlayableDataObject Interface.
     * It's public to allow its parent access to it when once its ready.
     * There is no way of knowing what data type this will be. (Possibly String, Number, or Boolean).
     *
     */
    public returnValue: any;



    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableGetVariableLogger = Log.getInstance().getLogger('GetVariablePlayableDataObject');
        this.playableGetVariableLogger.debug('constructorPreprocessing(): Playable GetVariable');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * <p>Run GetVariable.</p>
     *
     */
    public runFunction(): void {
        if (this.variableID) {
            console.log('!!!!!!!!!! ' + this.variableID);
            console.log('!!!!!!!!!! ' + this.variableService.findVariable(this.variableID));
            this.returnValue =  this.variableService.findVariable(this.variableID).value;
            this.playableGetVariableLogger.debug('returned variable value = ' + this.returnValue);
        } else {
            this.playableGetVariableLogger.debug('runFunction(): Error Missing required Data: VariableID');
        }
    }
}



