import { ElementDataObject } from '../../../../shared/system-shared/model/data-objects/element.data-object';
import { FunctionDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/functions/function.data-object';
import { GetMathDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/functions/get-math.data-object';
import { GetPropertyDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/functions/get-property.data-object';
import { GetRandomNumberDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/functions/get-random-number.data-object';
import { GetTypedConstantDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/functions/get-typed-constant.data-object';
import { GetVariableDataObject }
    from '../../../../shared/system-shared/model/data-objects/event-system/functions/get-variable.data-object';
import { GetMathPlayableDataObject } from './functions/get-math.playable.data-object';
import { GetPropertyPlayableDataObject } from './functions/get-property.playable.data-object';
import { GetRandomNumberPlayableDataObject } from './functions/get-random-number.playable.data-object';
import { GetTypedConstantPlayableDataObject } from './functions/get-typed-constant.playable.data-object';
import { GetVariablePlayableDataObject } from './functions/get-variable.playable.data-object';
import { Log, Logger } from '../../../../shared/system-shared/log';
import { ReturnValueFunctionPlayableDataObjectInterface }
    from './functions/return-value-function.playable.data-object.interface';
import { ValueDataObject } from '../../../../shared/system-shared/model/data-objects/event-system/value.data-object';


/**
 * Playable version of Value data object
 */
export class ValuePlayableDataObject extends ValueDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableValueLogger: Logger;

    /**
     * Reference to Function object that should be used to resolve the Value of this object
     */
    protected functionDataObject: ReturnValueFunctionPlayableDataObjectInterface;

    /**
     * <p>Return value of the ValueDataObject (typically set by the contained FunctionDataObject).
     * There is no way of knowing what data type this will be (String, Number, or Boolean depending
     * on returnValue of FunctionDataObject).<p>
     *
     */
    private _returnValue: any;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableValueLogger = Log.getInstance().getLogger('ValuePlayableDataObject');
        this.playableValueLogger.debug('constructorPreprocessing(): Playable Value');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * <p>Return value of the ValueDataObject.</p>
     *
     * @default true
     */
    public get returnValue(): any
    {
        this.executeGetValue();
        return this._returnValue;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * @param  element -> Element to be tested (typically by checking it's name property)
     */
    protected parseElement(element: ElementDataObject): void {
        super.parseElement(element);
        switch (element.name) {
            case FunctionDataObject.FUNCTION_ELEMENT_NAME:
                this.functionDataObject = this.createPlayableFunction(element);
                break;
        }
    }

    /**
     * @inheritDoc
     */
    protected createPlayableFunction(functionElement: ElementDataObject):
        ReturnValueFunctionPlayableDataObjectInterface {

        // Get Function Name
        const functionName: string = functionElement.getFirstChildElementByName('Name').value;

        switch (functionName) {
            case GetTypedConstantDataObject.NAME_ELEMENT_NAME:
                return new GetTypedConstantPlayableDataObject(functionElement);
            case GetPropertyDataObject.NAME_ELEMENT_NAME:
                return new GetPropertyPlayableDataObject(functionElement);
            case GetVariableDataObject.NAME_ELEMENT_NAME:
                 return new GetVariablePlayableDataObject(functionElement);
            case GetMathDataObject.NAME_ELEMENT_NAME:
                return new GetMathPlayableDataObject(functionElement);
            case GetRandomNumberDataObject.NAME_ELEMENT_NAME:
                return new GetRandomNumberPlayableDataObject(functionElement);
            default:
                this.playableValueLogger.debug(
                    'ERROR: parseElement: ' + functionName + ' Not A Vaild Return Value Function');
                return undefined;
        }
    }

    /**
     * <p>Runs runFunction() on the playable FunctionDataObject starting the process of getting the value.</p>
     */
    private executeGetValue(): void {
        this.functionDataObject.runFunction();
        // Get returnValue of FunctionDataObject
        this._returnValue = this.functionDataObject.returnValue;
    }
}



