import { EventEmitter } from '@angular/core';

import { ComponentDataObject }  from './component.data-object';
import { ElementDataObject }    from '../element.data-object';

import { Log, Logger }  from '../../../log';


/**
 * Data object for DIF video component.
 */
export class VideoDataObject extends ComponentDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    // TODO: Implement other properties of video if needed
    // private var imageLoader:VideoCapture;
    // public var URLFlvElement:Element;
    // public var URLF4vElement:Element;
    // public var URLMp4Element:Element;
    // public var URLWebmElement:Element;
    // public var URLOgvElement:Element;
    // public var URLAacElement:Element;
    // public var goToBlackElement:Element;
    // public var playOnLoadElement:Element;
    // public var durationElement:Element
    // public var displayControlsElement:Element;

    /**
     * The element that represents the Description element
     */
    private _description: ElementDataObject;

    /**
     * The element that represents the _URL contained in the button.
     *
     * @default undefined
     */
    private _url: ElementDataObject;

    /**
     * TODO: Add description.
     *
     * @default undefined
     */
    private _urlmp4: ElementDataObject;

    /**
     * TODO: Add description.
     *
     * @default undefined
     */
    private _urlwebm: ElementDataObject;

    /**
     * TODO: Add description.
     *
     * @default undefined
     */
    private _urlogv: ElementDataObject;

    /**
     * TODO: Add description.
     *
     * @default undefined
     */
    private _urlflv: ElementDataObject;

    /**
     * TODO: Add description.
     *
     * @default undefined
     */
    private _urlf4v: ElementDataObject;

    /**
     * The property used to emit start/pause playing event when set to true/false.
     */
    private _playing: boolean;

    /**
     * The property used to emit stop playing event when set to 0.
     */
    private _playhead: number = -1;

    /**
     * The property used to decide auto playing when loaded.
     */
    private _playOnLoad: boolean;

    /**
     * The property used to decide to show video controls when loaded.
     */
    private _displayControls: boolean;

    /**
     * The property used to decide to go to black when completed.
     */
    private _goToBlack: boolean;

    /**
     * When property _playing set to true, emit this event so video component subscribe to it can start to play.
     *
     * TODO: Change this to use Subject.
     *
     * @default An EventEmitter instance.
     */
    public onPlay: EventEmitter<any> = new EventEmitter();

    /**
     * When property _playing set to false, emit this event so video component subscribe to it can pause playing.
     *
     * TODO: Change this to use Subject.
     *
     * @default An EventEmitter instance.
     */
    public onPause: EventEmitter<any> = new EventEmitter();

    /**
     * When property _playhead set to 0, emit this event so video component subscribe to it can stop playing.
     *
     * TODO: Change this to use Subject.
     *
     * @default An EventEmitter instance.
     */
    public onStop: EventEmitter<any> = new EventEmitter();

    /**
     * An instance of the logger class for outputting log messages.
     *
     * A string is passed to the logger factory to be used as part of the name of the logger.  This name appears before
     * each log statement that is output.
     *
     * @default undefined
     */
    private movieDataObjectLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new VideoDataObject instance.
     */
    constructor(rootElement: ElementDataObject) {

        super(rootElement);
    }

    /**
     * @inheritDoc
     */
    protected constructorPreprocessing(): void {

        this.movieDataObjectLogger = Log.getInstance().getLogger('VideoDataObject');
        super.constructorPreprocessing();
    }

    /**
     * @inheritDoc
     */
    protected initializePropertyNames(): void {

        super.initializePropertyNames();

        this.initPropertyMapArray = this.initPropertyMapArray.concat([
            ['_playOnLoad',         'playOnLoad',       'PlayOnLoad',       'boolean'],
            ['_displayControls',    'displayControls',  'DisplayControls',  'boolean'],
            ['_playing',            'playing',          'Playing',          'boolean'],
            ['_playhead',           'playhead',         'Playhead',          'number'],
            ['_url',                'URL',              '_URL',             'element'],
            ['_urlmp4',             'URLmp4',           '_URLmp4',          'element'],
            ['_urlwebm',            'URLwebm',          '_URLwebm',         'element'],
            ['_urlogv',             'URLogv',           '_URLogv',          'element'],
            ['_urlflv',             'URLflv',           '_URLflv',          'element'],
            ['_urlf4v',             'URLf4v',           '_URLf4v',          'element'],
            ['_goToBlack',          'goToBlack',        'GoToBlack',        'boolean'],
            ['_description',        'description' ,     'Description',      'element']
        ]);
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The Description value. An Accessor for the Description element
     */
    public get description(): string {
        return this._description.value;
    }

    /**
     * The _URL contained in the video
     *
     * TODO: Add better description.
     * TODO: Add default value.
     *
     * @default
     */
    public get URL(): string {
        return this._url.value;
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get URLmp4(): string {
        return this._urlmp4.value;
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get URLwebm(): string {
        return this._urlwebm.value;
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get URLogv(): string {
        return this._urlogv.value;
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get URLflv(): string {
        return this._urlflv.value;
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get URLf4v(): string {
        return this._urlf4v.value;
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get playing(): boolean {
        return this._playing;
    }

    public set playing(value: boolean) {
        this._playing = value;
        if (this._playing) {
            this.onPlay.emit();
        } else {
            this.onPause.emit();
        }
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get playhead(): number  {
        return this._playhead;
    }

    public set playhead(value: number ) {
        this._playhead = value;
        if (this._playhead === 0) {
            this.onStop.emit();
        }
    }

    //////////////////////////////////////////////////////////

    /**
     * The property used to decide auto playing when loaded
     *
     * TODO: Add better description.
     * TODO: Add default value.
     *
     * @default
     */
    public get playOnLoad(): boolean {
        return this._playOnLoad;
    }

    public set playOnLoad(value: boolean) {
        this._playOnLoad = value;
    }

    //////////////////////////////////////////////////////////

    /**
     * The property used to decide to show video controls when loaded
     *
     * TODO: Add better description.
     * TODO: Add default value.
     *
     * @default
     */
    public get displayControls(): boolean {
        return this._displayControls;
    }

    public set displayControls(value: boolean) {
        this._displayControls = value;
    }

    //////////////////////////////////////////////////////////

    /**
     * TODO: Add description.
     * TODO: Add default value.
     *
     * @default
     */
    public get goToBlack(): boolean {
        return this._goToBlack;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Helper functions
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Concatenate class properties in a string.
     */
    public serializeToString(): string {

        let outString: string = 'Video Data Object:\n';

        outString += 'height = ' + this.height + '\n';
        outString += 'id = ' + this.id + '\n';
        outString += 'name = ' + this.name + '\n';
        outString += 'URL = ' + this.URL + '\n';
        outString += 'visible = ' + this.visible + '\n';
        outString += 'width = ' + this.width + '\n';
        outString += 'x = ' + this.x + '\n';
        outString += 'y = ' + this.y + '\n';
        outString += 'z = ' + this.z + '\n';
        outString += 'rootElement = ' + this.localRoot.name;

        return outString;
    }
}



