import {Component, Output, EventEmitter}    from '@angular/core';
import { UnitService }  from '../../../shared/system-shared/model/services/unit.service';
import {UnitDataObject} from '../../../shared/system-shared/model/data-objects/unit.data-object';
import {StartupConfigurationService} from '../../../model/services/startup-configuration-service';

@Component({
    selector: 'player-course-map',
    templateUrl: 'course-map.component.html',
    styleUrls:  [ '../user-interface.module.css' ]
})

/**
 *The CourseMap Component contains a list of unit-displays.
 */
export class CourseMapComponent {
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     *  Emits when any unit in the list has ben clicked.
     *
     * @type {EventEmitter<any>}
     */
    @Output() unitClicked: EventEmitter <any> = new EventEmitter<any>();

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     *  Constructor for CourseMapComponent.
     *
     * @param unitService
     * @param startUpConfig
     */
    constructor( private unitService: UnitService, private startUpConfig: StartupConfigurationService ) {}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * List of units to display in this component.
     * @returns {Array<UnitDataObject>}
     */
    public get units(): Array<UnitDataObject> {
        return this.unitService.getCourseMap();
    }

    /**
     * Sets if progress is locked on the Units to determine if they should be disabled.
     * @returns {boolean}
     */
    public get isProgressLocked(): boolean {
        return this.startUpConfig.config.lockProgress;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Event Handler
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Event handler to catch when one of the Unit-Displays are clicked
     */
    public unitClickedHandler(): void {
        this.unitClicked.emit(null);
    }
}



