import { BrowserModule }                from '@angular/platform-browser';
import { HttpModule }                   from '@angular/http';
import { MdButtonModule,
         MdDialog,
         MdDialogContainer,
         MdDialogModule,
         MdProgressSpinnerModule,
         MdSidenavModule }              from '@angular/material';
import { NgModule }                     from '@angular/core';

import { SimpleTimer }  from 'ng2-simple-timer';
import 'hammerjs';

import { PlayerModule }         from './player/player.module';
import { SystemSharedModule }   from './shared/system-shared/system-shared.module';
import { UserInterfaceModule }  from './user-interface/user-interface.module';

import { InjectorService }              from './shared/system-shared/model/services/injector.service';
import { PageService }                  from './shared/system-shared/model/services/page.service';
import { PlayerService }                from './model/services/player-service';
import {PopUpService}                   from './model/services/pop-up.service';
import { ReferenceService }             from './shared/system-shared/model/services/reference.service';
import { ScormService }                 from './model/services/scorm-service';
import { StartupConfigurationService }  from './model/services/startup-configuration-service';
import { UnitService }                  from './shared/system-shared/model/services/unit.service';
import { UnitTypeService }              from './shared/system-shared/model/services/unit-type.service';
import { VariableService }              from './shared/system-shared/model/services/variable.service';

import { AppComponent } from './app.component';
import {PopUpComponent} from './user-interface/view/pop-up.component';

@NgModule({
    declarations: [AppComponent],
    imports: [
        BrowserModule,
        HttpModule,
        MdButtonModule,
        MdProgressSpinnerModule,
        MdSidenavModule,
        PlayerModule,
        SystemSharedModule,
        UserInterfaceModule,
        MdDialogModule
    ],
    providers: [
        SimpleTimer,
        MdDialog,
        InjectorService,
        PageService,
        PlayerService,
        PopUpService,
        ReferenceService,
        ScormService,
        StartupConfigurationService,
        UnitService,
        UnitTypeService,
        VariableService
    ],
    entryComponents: [ MdDialogContainer, PopUpComponent ],
    bootstrap: [AppComponent]
})

/**
 * The root module of the Player application.
 */
export class AppModule { }



