import { XmlParser } from '../transactions/xml-parser';
import { Log, Logger } from '../../log';

/**
 * Desc: Class to hold Unit data and list of children objects
 */
export class UnitDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constants
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    public static readonly IMI_LEVEL: number = 1;
    public static readonly MODULE_LEVEL: number = 2;
    public static readonly TLO_LEVEL: number = 3;
    public static readonly ELO_LEVEL: number = 4;
    public static readonly PAGE_LEVEL: number = 5;

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    private _children: Array<UnitDataObject> = [];
    private _parent: UnitDataObject;
    private _createdBy: string;
    private _createdDt: string;
    private _hierarchyID: string;
    private _hierarchyIDIsTemp: string;
    private _modifiedBy: string;
    private _modifiedDt: string;
    private _pageID: string;
    private _parentHierarchyID: string;
    private _parentHierarchyIDIsTemp: string;
    private _sortOrder: number;
    private _thumbnailURL: string;
    private _unitDesc: string;
    private _unitID: string;
    private _unitIDIsTemp: string;
    private _unitLevel: string;
    private _unitName: string;
    private _unitStatusID: string;
    private _unitType: string;			// UnitTypeName
    private _unitTypeID: string;
    private _hasComments: boolean;

    private logger: Logger = Log.getInstance().getLogger('UnitDataObject');

    /**
     * An array to hold hierarchical(ancestor) units (include self) from top to bottom
     * for this unit, for example : array[0] = course unit, array[1] = module unit, ..array[5] = page unit
     * The index of this array is used to determined the hierarchy level
     * Note: this array will not be populated in constructing time because parenting is only set after constructing
     */
    public unitHierarchyArray: Array<UnitDataObject> = [];

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


    /**
     * Constructor - creates a new UnitDataObject instance.
     * If sourceXml is supplied, it will recursively build the whole unit tree structure objects
     *
     * @param   sourceXml   The root Element of the XML that contains the data to build this UnitDataObject from.
     */
    public constructor(sourceXml?: Element) {
        if (sourceXml) {
            this.constructFromXml(sourceXml);
        }
    }

    /**
     * This method populates the UnitataObject with data retrieved from XML.  It then calls the parseElement helper
     * method on each of its children.  Together these methods recursively create and populate this data object and all
     * of its descendants, building an entire hierarchy of UnitataObjects.
     *
     * @param   sourceXml   The root Element of the XML that contains the data to build this UnitataObject from.
     */
    private constructFromXml(sourceXml: Element): void {
        this.unitType = XmlParser.getAttribute(sourceXml, 'UnitType');
        this.hierarchyID = XmlParser.getAttribute(sourceXml, 'HierarchyID');
        this.parentHierarchyID = XmlParser.getAttribute(sourceXml, 'ParentHierarchyID');
        this.unitLevel = XmlParser.getAttribute(sourceXml, 'UnitLevel');

        const sourceElements: Array<Element> = XmlParser.getChildElements(sourceXml);
        for ( const element of sourceElements) {
            this.parseElement(element);
        }
        if ( this.children.length > 0 ) {
            this.sortMyChildren();
        }
    }

     /**
      * This method is a helper of constructFromXml.  It determines if the given XML Element contains a property of this
      * UnitataObject or one of its children.  Together these methods recursively create and populate this data
      * object and all of its descendants, building an entire hierarchy of UnitataObjects.
      *
      *
      * @param  sourceElement   The XML Element we are determining to be either a property of this UnitataObject, or
      *                         an actual child.
      */
     protected parseElement(sourceElement: Element): void {
        switch (sourceElement.tagName) {
            case 'UnitLevel':
                this.unitLevel = XmlParser.getTextContent(sourceElement);
                break;
            case 'UnitType':
                this.unitType = XmlParser.getTextContent(sourceElement);
                break;
            case 'UnitName':
                this.unitName = XmlParser.getTextContent(sourceElement);
                break;
            case 'UnitID':
                this.unitID = XmlParser.getTextContent(sourceElement);
                break;
            case 'UnitDesc':
                this.unitDesc = XmlParser.getTextContent(sourceElement);
                break;
            case 'SortOrder':
                this.sortOrder = + XmlParser.getTextContent(sourceElement);
                break;
            case 'PageID':
                this.pageID = XmlParser.getTextContent(sourceElement);
                break;
            case 'ThumbnailURL':
                this.thumbnailURL = XmlParser.getTextContent(sourceElement);
                break;
            case 'CreatedBy':
                this.createdBy = XmlParser.getTextContent(sourceElement);
                break;
            case 'CreatedDT':
                this.createdDt = XmlParser.getTextContent(sourceElement);
                break;
            case 'ModifiedBy':
                this.modifiedBy = XmlParser.getTextContent(sourceElement);
                break;
            case 'ModifiedDT':
                this.modifiedDt = XmlParser.getTextContent(sourceElement);
                break;
            case 'CommentExisted':
                this.hasComments = (XmlParser.getTextContent(sourceElement).toLowerCase() === 'true');
                break;
            case 'Unit':
                // This Element actually contains a real child Unit DataObject
                this.createNewUnit(sourceElement);
                break;
        }
     }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////


    public get parent(): UnitDataObject {
        return this._parent;
    }

    public set parent(value: UnitDataObject) {
        this._parent = value;
    }


    public get children(): Array<UnitDataObject> {
        return this._children;
    }

    public get createdBy(): string {
        return this._createdBy;
    }

    public set createdBy(value: string) {
        this._createdBy = value;
    }

    public get createdDt(): string {
        return this._createdDt;
    }

    public set createdDt(value: string) {
        this._createdDt = value;
    }

    public get hierarchyID(): string {
        return this._hierarchyID;
    }

    public set hierarchyID(value: string) {
        this._hierarchyID = value;
    }

    public get hierarchyIDIsTemp(): string {
        return this._hierarchyIDIsTemp;
    }

    public set hierarchyIDIsTemp(value: string) {
        this._hierarchyIDIsTemp = value;
    }

    public get modifiedBy(): string {
        return this._modifiedBy;
    }

    public set modifiedBy(value: string) {
        this._modifiedBy = value;
    }

    public get modifiedDt(): string {
        return this._modifiedDt;
    }

    public set modifiedDt(value: string) {
        this._modifiedDt = value;
    }

    public get pageID(): string {
        return this._pageID;
    }

    public set pageID(value: string) {
        this._pageID = value;
    }

    public get parentHierarchyID(): string {
        return this._parentHierarchyID;
    }

    public set parentHierarchyID(value: string) {
        this._parentHierarchyID = value;
    }

    public get parentHierarchyIDIsTemp(): string {
        return this._parentHierarchyIDIsTemp;
    }

    public set parentHierarchyIDIsTemp(value: string) {
        this._parentHierarchyIDIsTemp = value;
    }

    public get sortOrder(): number {
        return this._sortOrder;
    }

    public set sortOrder(value: number) {
        this._sortOrder = value;
    }

    public get thumbnailURL(): string {
        return this._thumbnailURL;
    }

    public set thumbnailURL(value: string) {
        this. _thumbnailURL = value;
    }

    public get unitDesc(): string {
        return this._unitDesc;
    }

    public set unitDesc(value: string) {
        this._unitDesc = value;
    }

    public get unitID(): string {
        return this._unitID;
    }

    public set unitID(value: string) {
        this._unitID = value;
    }

    public get unitIDIsTemp(): string {
        return this._unitIDIsTemp;
    }

    public set unitIDIsTemp(value: string) {
        this._unitIDIsTemp = value;
    }

    public get unitLevel(): string {
        return this._unitLevel;
    }

    public set unitLevel(value: string) {
        this._unitLevel = value;
    }

    public get unitName(): string {
        return this._unitName;
    }

    public set unitName(value: string) {
        this._unitName = value;
    }

    public get unitStatusID(): string {
        return this._unitStatusID;
    }

    public set unitStatusID(value: string) {
        this._unitStatusID = value;
    }

    public get unitType(): string {
        return this._unitType;
    }

    public set unitType(value: string) {
        this._unitType = value;
    }

    public get unitTypeID(): string {
        return this._unitTypeID;
    }

    public set unitTypeID(value: string) {
        this._unitTypeID = value;
    }

    public get hasComments(): boolean {
        return this._hasComments;
    }

    public set hasComments(value: boolean) {
        this._hasComments = value;
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
    * create new unit data object and tie parent-child together
    *
    */
    public createNewUnit(sourceElement: Element): void {
        const newChild: UnitDataObject = new UnitDataObject();
        this._children.push(newChild);
        newChild.parent = this;
        newChild.constructFromXml(sourceElement);
    }


    /**
     * 1. Recursively loop children units to populate supplied unit array
     * 2. Build unitHierarchy for each unit
     */
    public buildUnitArray(unitArray: Array<UnitDataObject>): void {
        this.buildHierarchy();
        for (const childUnit of this.children ) {
            unitArray.push(childUnit);
            childUnit.buildUnitArray(unitArray);
        }
    }

    /**
     * Add all hierarchical(ancestor) units (includes self) to the unitHierarchy Array
     *
     */
    private buildHierarchy (): void {
        if (this.parent) {
            this.unitHierarchyArray.push(...this.parent.unitHierarchyArray);

            // Add empty unit level ancestors, for example, if a page is directly under a course,
            // we need to add Module, TLO, ELO placeholders between Course and Page
            for (let i: number = this.unitHierarchyArray.length; i < +this.unitLevel - 1; i++) {
                this.unitHierarchyArray.push(undefined);
            }
        }
        // Add self to array too
        this.unitHierarchyArray.push(this);
    }


    /**
    * Sort children based on their sortOrder property
    *
    */
    public sortMyChildren(): void {
        this._children.sort( (unit1, unit2 ) => {
            return unit1.sortOrder - unit2.sortOrder;
        });
    }

    public toString(tab: string = ''): string {
        let outString: string = '';

        outString += tab + 'unitName = ' + this._unitName + '\n';
        outString += tab + 'children.length = ' + this._children.length + '\n';
        outString += tab + 'createdBy = ' + this._createdBy + '\n';
        outString += tab + 'createdDt = ' + this._createdDt + '\n';
        outString += tab + 'hierarchyID = ' + this._hierarchyID + '\n';
        outString += tab + 'hierarchyIDIsTemp = ' + this._hierarchyIDIsTemp + '\n';
        outString += tab + 'modifiedBy = ' + this._modifiedBy + '\n';
        outString += tab + 'modifiedDt = ' + this._modifiedDt + '\n';
        outString += tab + 'pageID = ' + this._pageID + '\n';
        outString += tab + 'parentHierarchyID = ' + this._parentHierarchyID + '\n';
        outString += tab + 'parentHierarchyIDIsTemp = ' + this._parentHierarchyIDIsTemp + '\n';
        outString += tab + 'sortOrder = ' + this._sortOrder + '\n';
        outString += tab + 'thumbnailURL = ' + this._thumbnailURL + '\n';
        outString += tab + 'unitDesc = ' + this._unitDesc + '\n';
        outString += tab + 'unitID = ' + this._unitID + '\n';
        outString += tab + 'unitIDIsTemp = ' + this._unitIDIsTemp + '\n';
        outString += tab + 'unitLevel = ' + this._unitLevel + '\n';
        outString += tab + 'unitStatusID = ' + this._unitStatusID + '\n';
        outString += tab + 'unitType = ' + this._unitType + '\n';
        outString += tab + 'unitTypeID = ' + this._unitTypeID + '\n';
        outString += tab + 'hasComments = ' + this._hasComments + '\n';

        return outString;
    }

    /**
     * Recursively log debug unit tree starting with supplied three level
     *
     * @param level
     */
    public traceMe(level: number = 0): void {
        let tab: string = '';
        for (let i: number = 0; i < level; i++) {
            tab = tab + '\t';
        }

        this.logger.debug('\n' + this.toString(tab));

        for (let j: number = 0; j < this.children.length; j++) {
            this.children[j].traceMe(level + 1);
        }
    }

}



