import { ComponentDataObject } from './component.data-object';
import { ElementDataObject } from '../element.data-object';
import { Log, Logger } from '../../../log';

/*
*
* Data object for DIF audio component
*
* */
export class HtmlDataObject extends ComponentDataObject {


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    // TODO: Implement other properties of HTML if needed

    /**
     * The element that represents the _URL element
     */
    private _url: ElementDataObject;

    /**
     * The element that represents the Description element
     */
    private _description: ElementDataObject;

    /**
     * Reference to logger object for logging
     */
    private htmlDataObjectLogger: Logger;

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Data object for DIF html component - constructor
     */
    constructor(rootElement: ElementDataObject) {
        super(rootElement);
    }

    /**
     * Overrides base constructorPreprocessing method.
     */
    protected constructorPreprocessing(): void {
       this.htmlDataObjectLogger = Log.getInstance().getLogger('HtmlDataObject');
       super.constructorPreprocessing();
    }


    /**
     * overrides base version to add specific properties
     */
    protected initializePropertyNames(): void {
        super.initializePropertyNames();
        this.initPropertyMapArray = this.initPropertyMapArray.concat([
            ['_url', 'URL', '_URL', 'element'],
            ['_description', 'description' , 'Description', 'element']
        ]);
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The _URL contained in the html
     */
    public get URL(): string {
        return this._url.value;
    }

    /**
     * The Description value. An Accessor for the Description element
     */
    public get description(): string {
        return this._description.value;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Helper functions
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Concatenate class properties into a string
     */
    public serializeToString(): string {
        let outString: string = 'HTML Data Object:\n';

        outString += 'height = ' + this.height + '\n';
        outString += 'id = ' + this.id + '\n';
        outString += 'name = ' + this.name + '\n';
        outString += 'URL = ' + this.URL + '\n';
        outString += 'visible = ' + this.visible + '\n';
        outString += 'width = ' + this.width + '\n';
        outString += 'x = ' + this.x + '\n';
        outString += 'y = ' + this.y + '\n';
        outString += 'z = ' + this.z + '\n';
        outString += 'rootElement = ' + this.localRoot.name;

        return outString;
    }
}




