/* tslint:disable:max-line-length */
import { InjectorService }              from '../../../shared/system-shared/model/services/injector.service';
import { StartupConfigurationService }  from '../../services/startup-configuration-service';

import { ManifestDataObject }       from '../../data-objects/manifest.data-object';
import { TransactionTaskHandler }   from '../../../shared/system-shared/model/transactions/transaction-task-handlers/transaction-task-handler';
/* tslint:enable:max-line-length */


/**
 * This task handler makes the request to load the application's manifest file.  The response is then parsed and from
 * that data a manifest data object is created in the startup configuration service for use by the rest of the
 * application.
 *
 * @see TransactionTaskHandler
 * @see StartupConfigurationService
 * @see ManifestDataObject
 */
export class LoadManifestTaskHandler extends TransactionTaskHandler {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new LoadManifestTaskHandler instance.
     */
    constructor() {

        super('LoadManifest');
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Set our source file URL after the super's implementation sets the data source.
     *
     * @inheritDoc
     */
    protected setDataSource(): void {

        super.setDataSource();
        this.fileUrl = './IMImanifest.xml';
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Request Handling
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Builds the XML for load manifest request.
     *
     * The request is initiated on our behalf by the Transaction class that instantiates us.  It also returns to us the
     * response if a valid one is received.
     *
     * @returns The root element of the load manifest request XML.
     */
    public buildRequestXml(): Element {

        return this.requestXml;
    }

    /**
     * Parses the received XML and creates a ManifestDataObject in the StartupConfigurationService.
     *
     * @inheritDoc
     */
    public processResponse(): void {

        const startupConfigurationService: StartupConfigurationService =
            InjectorService.injector.get(StartupConfigurationService);
        startupConfigurationService.manifest = new ManifestDataObject(this.responseData);
    }
}



