import { CommonModule }             from '@angular/common';
import { HttpModule }               from '@angular/http';
import { MdButtonModule,
         MdIconModule,
         MdListModule,
         MdTooltipModule,
         MdSliderModule }           from '@angular/material';
import { NgModule }                 from '@angular/core';

import {DragulaModule}              from 'ng2-dragula';

import { AppBarComponent }              from './view/app-bar.component';
import { PageIndicatorComponent }       from './view/sub-component/page-indicator.component';
import { PathedPageTitleComponent }     from './view/sub-component/pathed-page-title.component';
import { CommentsButtonComponent }      from './view/comments-button.component';
import { ControlBarComponent }          from './view/control-bar.component';
import { PopUpComponent }               from './view/pop-up.component';
import { SideNavContentComponent }      from './view/sidenav-content.component';
import { CourseMapComponent }           from './view/sidenav-components/course-map.component';
import { UnitDisplayComponent }         from './view/sidenav-components/unit-display.component';
import { ReferencesComponent }          from './view/sidenav-components/references.component';
import { ReferenceDisplayComponent }    from './view/sidenav-components/reference-display.component';


@NgModule({
    // TODO: If we add more modules we might want to make a shared module.
    imports: [
        CommonModule,
        DragulaModule,
        HttpModule,
        MdButtonModule,
        MdIconModule,
        MdListModule,
        MdTooltipModule,
        MdSliderModule,
    ],
    declarations: [
        AppBarComponent,
        PageIndicatorComponent,
        PathedPageTitleComponent,
        CommentsButtonComponent,
        ControlBarComponent,
        PopUpComponent,
        SideNavContentComponent,
        CourseMapComponent,
        UnitDisplayComponent,
        ReferencesComponent,
        ReferenceDisplayComponent,
    ],
    exports: [ AppBarComponent, ControlBarComponent, SideNavContentComponent, PopUpComponent, CommentsButtonComponent ]
})

/**
 * TODO: Add class description.
 */
export class UserInterfaceModule { }



