import { Observable }   from 'rxjs/Observable';

import { Transaction }  from '../../shared/system-shared/model/transactions/transaction';

import { LoadPageDirectoryTaskHandler } from './transaction-task-handlers/load-page-directory.task-handler';


/**
 * This class is responsible for loading page directory data from the various TransactionDataSources
 * (server/file/memory).
 *
 * @see Transaction
 * @see TransactionDataSource
 */
export class LoadPageDirectoryTransaction extends Transaction {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * Constructor - creates a new LoadPageDirectoryTransaction instance.
     */
    constructor() {

        super('LoadPageDirectory');
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * @inheritDoc
     */
    public startTransaction(): Observable<any> {

        // add TaskHandler for loading PageDirectory data
        const taskHandler: LoadPageDirectoryTaskHandler = new LoadPageDirectoryTaskHandler();
        taskHandler.buildRequestXml();
        this.addTaskHandler(taskHandler);

        // Send request to get processed
        return super.startTransaction();
    }
}



