import { ComponentDataObject }
    from '../../../../../shared/system-shared/model/data-objects/components/component.data-object';
import { ElementDataObject } from '../../../../../shared/system-shared/model/data-objects/element.data-object';
import { GetPropertyDataObject }
    from '../../../../../shared/system-shared/model/data-objects/event-system/functions/get-property.data-object';
import { InjectorService } from '../../../../../shared/system-shared/model/services/injector.service';
import { Log, Logger } from '../../../../../shared/system-shared/log';
import { PageService } from '../../../../../shared/system-shared/model/services/page.service';
import { ReturnValueFunctionPlayableDataObjectInterface } from './return-value-function.playable.data-object.interface';


/**
 * Playable version of GetProperty data object
 */
export class GetPropertyPlayableDataObject extends GetPropertyDataObject
    implements ReturnValueFunctionPlayableDataObjectInterface {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private playableGetPropertyLogger: Logger;

    private pageService: PageService = InjectorService.injector.get(PageService);

    /**
     * <p>The return value of the playable FunctionDataObject implements ReturnValueFunctionPlayableDataObjectInterface
     * It's public to allow its parent access to it when once its ready.
     * There is no way of knowing what data type this will be. (Possibly String, Number, or Boolean).</p>
     *
     * @internal: Maybe we could use string|number|boolean?
     */
    public returnValue: any;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Constructor
     * @param sourceElement -> root level ElementDataObject for populating this data object and its children (though
     *                          it won't break, should not be null / undefined)
     */
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.playableGetPropertyLogger = Log.getInstance().getLogger('GetPropertyPlayableDataObject');
        this.playableGetPropertyLogger.debug('constructorPreprocessing(): Playable GetProperty');
        super.constructorPreprocessing();
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * <p>Run GetProperty.</p>
     *
     */
    public runFunction(): void {
        if (this.componentElementID && this.propertyName) {
            const component: ComponentDataObject  =  this.pageService.getComponent(+this.componentElementID);
            if (component && component.getPropertyMap(this.propertyName)) {
                this.playableGetPropertyLogger.debug('runFunction(): property found - ' + this.propertyName);
                this.returnValue = component[component.getPropertyMap(this.propertyName).publicName];
                this.playableGetPropertyLogger.debug('returned property value = ' + this.returnValue);
            } else {
                this.playableGetPropertyLogger.debug('ERROR: runFunction(): component (' + component.name +
                    ') property (' + this.propertyName + ') not found');
            }
        } else {
            this.playableGetPropertyLogger.debug(
                'runFunction(): Error Missing required Data: ComponentElementID and PropertyName');
        }
    }

}



