import { Directive }    from '@angular/core';

import { EventHandlerDirective }    from './event-handler.directive';

import { EventDataObject }  from '../../../shared/system-shared/model/data-objects/event-system/event.data-object';
import { VideoDataObject }  from '../../../shared/system-shared/model/data-objects/components/video.data-object';


@Directive({
  selector: '[playerVideoEventHandler]'
})


/**
 * TODO: Add description.
 */
export class VideoEventHandlerDirective extends EventHandlerDirective {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Angular Lifecycle Hooks
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    ngOnInit(): void {

        super.ngOnInit();

        // Add video play/pause/stop related event emitter subscription
        this.addSubscription();

        // Always add on complete listener
        // TODO: This event listener is never removed (released), it could cause memory/performance issue ...
        this.createEventListener('ended', EventDataObject.ON_COMPLETE);
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * @inheritDoc
     */
    protected parseEventTriggers(eventType: string): void {

        super.parseEventTriggers(eventType);

        switch (eventType) {
            case EventDataObject.ON_PLAY:
                this.createEventListener('play', eventType);
                break;
            case EventDataObject.ON_PAUSE:
                this.createEventListener('pause', eventType);
                break;
        }
    }

    /**
     * @inheritDoc
     */
    protected processEvent(event: Event, eventType: string): void {

        super.processEvent(event, eventType);

        // Emit Video's onComplete event
        const movieDataObject: VideoDataObject = this.dataSource as VideoDataObject;
        movieDataObject.onComplete.emit();
    }

    /**
     * Add video play/pause/stop related event emitter subscription.
     */
    protected addSubscription(): void {

        const videoDataObject: VideoDataObject = this.dataSource as VideoDataObject;
        const htmlVideoElement: HTMLVideoElement = this.elementRef.nativeElement as HTMLVideoElement;

        videoDataObject.onPlay.subscribe( () => {
            htmlVideoElement.play();
        } );
        videoDataObject.onPause.subscribe( () => {
            htmlVideoElement.pause();
        } );
        videoDataObject.onStop.subscribe( () => {
            htmlVideoElement.pause();
            htmlVideoElement.currentTime = 0;
        } );
    }
}



