import {Component, Input, Output, OnInit, EventEmitter} from '@angular/core';
import {PageService} from '../../../shared/system-shared/model/services/page.service';
import {UnitDataObject} from '../../../shared/system-shared/model/data-objects/unit.data-object';
import {UnitService} from '../../../shared/system-shared/model/services/unit.service';

@Component({
    selector: 'player-unit-display',
    templateUrl: 'unit-display.component.html',
    styleUrls:  [ '../user-interface.module.css' ]
})

/**
 * The UnitDisplayComponent displays a navigable Unit. It's disables according to course progression. And indents
 * According to hierarchy level.
 */
export class UnitDisplayComponent implements OnInit {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Instance Variables / Properties
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     * The unit being displayed by this component
     */
    @Input()
    unit: UnitDataObject;

    /**
     * If progress lock is on.
     */
    @Input()
    isProgressLocked: boolean;

    /**
     * Emits onclick of this component
     */
    @Output() unitClicked: EventEmitter<any> = new EventEmitter<any>();

    /**
     * The PageID we will navigate to when this is clicked.
     */
    private pageIDToNavigateTo: string;

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     *  The Constructor for the UnitDisplayComponent
     *
     * @param unitService
     * @param pageService
     */
    constructor( private unitService: UnitService, private pageService: PageService) {}


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Accessors
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     *  Name of the Unit this Component represents
     * @returns {string}
     */
    public get unitName(): string {
        return this.unit.unitName;
    }

    /**
     * Determines if this component is Disabled.
     *
     * @returns {boolean}
     */
    public get isDisabled(): boolean{
        if (!this.pageIDToNavigateTo) { return true; }
        if (!this.isProgressLocked) { return false; }
        return (this.unitService.isPageAfterHighestPageProgress( this.pageIDToNavigateTo ));
    }

    /**
     *  Determines how much to indent this unit.
     *
     * @returns {number}
     */
    public get indentAmount(): number{
        return (+this.unit.unitLevel * 15) - 10;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Event Handlers
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     *  Event Handler for when this component is clicked. Emits this components unitClicked Event.
     */
    public onClick(): void {
        this.pageService.loadPage(this.pageIDToNavigateTo);
        this.unitClicked.emit(null);
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Angular Component Lifecycle Hooks
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

    /**
     *  OnInt setup pageIDToNavigateTo
     */
    ngOnInit(): void {
        if ( this.unit ) {
            const pageUnit: UnitDataObject = this.unitService.getFirstPageInUnit(this.unit);
            // find out if first page in unit exists before we set pageIDToNavigateTo
            if (pageUnit) {
                this.pageIDToNavigateTo = pageUnit.pageID;
            }
        }
    }
}





