import { FrameworkEventDataObject }
    from '../../../../shared/system-shared/model/data-objects/action-system/framework-event.data-object';
import { Log, Logger } from '../../../../shared/system-shared/log';
import { ElementDataObject } from '../../../../shared/system-shared/model/data-objects/element.data-object';
import { FrameworkFunctionPlayableDataObject } from './framework-function.playable.data-object';


/**
 * Playable version of FrameworkEventDataObject
 * @inheritDoc
 */
export class FrameworkEventPlayableDataObject extends FrameworkEventDataObject {

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Properties / Instance Variables
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Logger instance for logging
     */
    private frameworkEventPlayableLogger: Logger;


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Constructor / Initialization
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    constructor(sourceElement: ElementDataObject) {
        super(sourceElement);
    }

    /**
     * Overrides base version.  Applies class specific preprocessing.
     */
    protected constructorPreprocessing(): void {
        this.frameworkEventPlayableLogger = Log.getInstance().getLogger('FrameworkEventDataObject');
        this.frameworkEventPlayableLogger.debug('constructorPreprocessing(): Playable FrameworkEvent');
        super.constructorPreprocessing();

        // NOTE: Variable initialization needs to be done here.  If done with the declaration, somehow it isn't
        //          initialized in time for when these methods are called via the constructor of the super class.
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    ////////// Functions / Methods
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    /**
     * Overrides base.  Creates playable version of FrameworkFunction.
     *
     * @param element -> root level Element to construct FrameworkFunction from
     * @returns {FrameworkFunctionPlayableDataObject}
     */
    protected createFrameworkFunction(element: ElementDataObject): FrameworkFunctionPlayableDataObject {
        return new FrameworkFunctionPlayableDataObject(element);
    }

    /**
     * Starts processing FrameworkEvent
     */
    public processFrameworkEvent(): void {
        this.frameworkEventPlayableLogger.debug('processFrameworkEvent()');
        (this.frameworkFunction as FrameworkFunctionPlayableDataObject).processFrameworkFunction();
    }
}



